<template>
	<div class="profile-form">
		<b-form @submit.prevent="validateBeforeSubmit">
			<div class="modal-height-fix">
				<div class="row form-group">
					<div class="col-sm-3">
						<div class="profile-avatar img-avatar">
							<div v-if="!image" class="img-avatar_less" @click="trigger">
								<i class="profile-avatar__icon ic_image"></i>
								<input type="file" @change="onFileChange" ref="onFileChange">
							</div>
	
							<div v-else>
								<div class="img-avatar_size img-avatar">
									<img :src="image" />
								</div>
								<i @click="removeImage" class="ic_edit img-avatar_remove"></i>
							</div>
						</div>
	
					</div>
					<div class="col-sm-9">
						<div class="row">
							<div class="col">
								<b-input id="firstName_formAplicate" type="text" placeholder="" maxlength="30" name="firstName_formAplicate" v-model="firstName_formAplicate" v-validate="'required|alpha'"
								 :class="{'input': true, 'border-danger': errors.has('firstName_formAplicate') }"></b-input>
								<span v-show="errors.has('firstName_formAplicate')" class="help-valid text-danger">this field required</span>

							</div>
							<div class="col">
								<b-input id="lastName_formApplicate" type="text" placeholder=" " maxlength="30" name="lastName_formApplicate" v-model="lastName_formApplicate" v-validate="'required|alpha'"
								 :class="{'input': true, 'border-danger': errors.has('lastName_formApplicate') }"></b-input>
								<span v-show="errors.has('lastName_formApplicate')" class="help-valid text-danger">this field required</span>

							</div>
						</div>
					</div>
	
				</div>
	
				<fieldset class="b-form-group form-group">
					<div class="form-row">
						<legend class="col-form-legend col-sm-3">
							<i class="ic_assign mr-1"></i>
							<span></span>
						</legend>
						<div class="col-sm-9">
							<multiselect v-model="value" :options="options" :multiple="true" :close-on-select="false" :clear-on-select="false" :hide-selected="true"
							 :preserve-search="true" placeholder="" label="name" track-by="name">
								<template slot="tag" slot-scope="props">
									<span class="multiselect__tag">
										<span>{{ props.option.language }}</span>
										<i aria-hidden="true" tabindex="1" class="multiselect__tag-icon" @click="props.remove(props.option)"></i>
									</span>
								</template>
							</multiselect>
						</div>
					</div>
				</fieldset>
	
	
	
				<div class="dynamic-input">
	
					<b-form-fieldset label="<i class='ic_email mr-1'></i> " :label-cols="3" :horizontal="true">
						<b-form-input name="email" v-model="email" v-validate="'required|email'" :class="{'input': true, 'border-danger': errors.has('email') }"
						 placeholder="   " maxlength="100"></b-form-input>
						<span v-show="errors.has('email')" class="help-valid text-danger">this field required</span>

						<div class="position-relative" v-for="(input, index) in emails">
							<b-form-input type="email" placeholder="   " v-model="input.item" maxlength="100"></b-form-input>
							<i class="ic_close dynamic-input_remover" @click="deleteInput('deleteEmail',index)"></i>
						</div>
	
						<i class="ic_add dynamic-input_adder" @click="addInput('addEmail')" v-if="emails.length < 1"></i>
					</b-form-fieldset>
	
					<b-form-fieldset label="<i class='ic_phone mr-1'></i> " :label-cols="3" :horizontal="true">
						<b-form-input name="phone" v-model="phone" v-validate="'numeric'" :class="{'input': true, 'border-danger': errors.has('phone') }"
						 type="text" placeholder="  " maxlength="30"></b-form-input>
						 <span v-show="errors.has('phone')" class="help-valid text-danger">this field required</span>
						<div class="position-relative" v-for="(input, index) in phones">
							<b-form-input type="text" placeholder="  " v-model="input.item" maxlength="30"></b-form-input>
							<i class="ic_close dynamic-input_remover" @click="deleteInput('deletePhone',index)"></i>
						</div>
						<i class="ic_add dynamic-input_adder" @click="addInput('addPhone')" v-if="phones.length < 1"></i>
					</b-form-fieldset>
	
				</div>
	
				<div role="tablist" class="tablist-accordion">
					<b-card no-body>
						<b-card-header header-tag="header" class="p-1" role="tab">
							<span class="accordion-title" block href="#" v-b-toggle.accordion1> 
								<span class="accordion-title__select"></span>
							</span>
						</b-card-header>
						<b-collapse id="accordion1" accordion="my-accordion" role="tabpanel">
							<b-card-body>
								<fieldset class="b-form-group form-group">
									<div class="form-row">
										<legend class="col-form-legend col-sm-3">
											<i class='ic_sources mr-1'></i>
											<span></span>
										</legend>
										<div class="col-sm-9">
											<b-input id="inlineInput13" type="text" placeholder="  " maxlength="30"></b-input>
										</div>
									</div>
								</fieldset>
	
								<fieldset class="b-form-group form-group">
									<div class="form-row">
										<legend class="col-form-legend col-sm-3">
											<i class='ic_tags mr-1'></i>
											<span> </span>
										</legend>
										<div class="col-sm-9">
											<multiselect v-model="valueTag" tag-placeholder="   " placeholder="   " label="name" track-by="code"
											 :options="optionsTag" :multiple="true" :taggable="true" @tag="addTag"></multiselect>
										</div>
									</div>
								</fieldset>
	
								<b-form-fieldset label="<i class='ic_social mr-1'></i>  " :label-cols="3" :horizontal="true">
									<b-form-input type="text" placeholder="   "></b-form-input>
									<div class="position-relative" v-for="(input, index) in socials">
										<b-form-input type="text" placeholder="   " v-model="input.item"></b-form-input>
										<i class="ic_close dynamic-input_remover" @click="deleteInput('deleteSocial',index)"></i>
									</div>
									<i class="ic_add dynamic-input_adder" @click="addInput('addSocial')"></i>
								</b-form-fieldset>
	
								<b-form-fieldset label="<i class='ic_link mr-1'></i> " :label-cols="3" :horizontal="true">
									<b-form-input type="text" placeholder="  " maxlength="300"></b-form-input>
									<div class="position-relative" v-for="(input, index) in links">
										<b-form-input type="text" placeholder="  " v-model="input.item" maxlength="300"></b-form-input>
										<i class="ic_close dynamic-input_remover" @click="deleteInput('deleteLink',index)"></i>
									</div>
									<i class="ic_add dynamic-input_adder" @click="addInput('addLink')" v-if="links.length < 8"></i>
								</b-form-fieldset>
								<b-form-fieldset v-for="(input,label, index) in addFields">
									<div class="form-row">
										<legend class="col-form-legend col-sm-3">
											<b-form-input type="text" placeholder="  " v-model="input.label" maxlength="200"></b-form-input>
										</legend>
										<div class="col-form-legend col-sm-9">
											<div class="position-relative">
												<b-form-input type="text" placeholder=" " v-model="input.item" maxlength="200"></b-form-input>
												<i class="ic_close dynamic-input_remover" @click="deleteInput('deleteField',index)"></i>
											</div>
										</div>
									</div>
								</b-form-fieldset>
	
								<div class="b-form-group form-group">
									<b-button type="button" size="sm" variant="default" @click="addInput('addField')">
										<span> </span>
										&nbsp;&nbsp;
										<span class="ic_add"></span>
									</b-button>
								</div>
	
							</b-card-body>
						</b-collapse>
					</b-card>
				</div>
				<b-form-fieldset label="" :label-cols="3" :horizontal="false" class="mt-3 profile-form_border-desc">
					<b-form-textarea type="text" :rows="3" :max-row="6" placeholder="  " maxlength="1000"></b-form-textarea>
				</b-form-fieldset>
	
				<div class="upload">
					<ul>
						<li class="mb-3" v-for="(file, index) in files" :key="file.id">
	
							<div class="row">
								<div class="col-6">
									<div class="pt-1">{{file.name}}</div>
								</div>
								<div class="col-3">
									<div class="progress mt-2" v-if="file.active || file.progress !== '0.00'">
										<div :class="{'progress-bar': true, 'progress-bar-striped': true, 'bg-danger': file.error, 'progress-bar-animated': file.active}"
										 role="progressbar" :style="{width: file.progress + '%'}">
											<span>{{file.progress}}%</span>
											<span v-if="file.error"> - {{file.error}}</span>
											<span v-else-if="file.success"> - </span>
										</div>
									</div>
								</div>
								<div class="col-3">
									<button class="btn btn-link" type="button" @click.prevent="remove(file)"></button>
								</div>
	
							</div>
	
						</li>
					</ul>
				</div>
			</div>
			<div slot="footer">
				<b-button type="submit" size="sm" variant="primary"></b-button>
				<!-- <b-button type="button" size="sm" variant="success" class="ml-1">file upload</b-button> -->
				<file-upload class="btn btn-success mb-0" post-action="/upload/post" extensions="gif,jpg,jpeg,png,webp" accept="image/png,image/gif,image/jpeg,image/webp"
				 :multiple="true" :size="1024 * 1024 * 10" v-model="files" @input-filter="inputFilter" @input-file="inputFile" ref="upload">
					<i class="fa fa-plus"></i>
					 
				</file-upload>

			</div>
		</b-form>
	</div>
</template>

<script>
	// for selecting and taging
	import Multiselect from "vue-multiselect";
	import FileUpload from 'vue-upload-component'

	export default {
		name: "form-applicat",
		components: {
			// for selecting and taging
			Multiselect,
			FileUpload,

		},
		data() {
			return {
				image: "",
				emails: [],
				phones: [],
				socials: [],
				links: [],
				firstName_formAplicate: '',
				lastName_formApplicate: '',
				email: '',
				phone: '',
				addFields: [],

				// files upload from FileUpload
				files: [],



				// for selecting and taging
				value: [
					{ name: 'Vue.js', language: 'JavaScript' }
				],
				options: [
					{ name: 'Vue.js', language: 'JavaScript' },
					{ name: 'Adonis', language: 'JavaScript' },
					{ name: 'Rails', language: 'Ruby' },
					{ name: 'Sinatra', language: 'Ruby' },
					{ name: 'Laravel', language: 'PHP' },
					{ name: 'Phoenix', language: 'Elixir' }
				],
				valueTag: [{ name: "Javascript", code: "js" }],
				optionsTag: [
					{ name: "Vue.js", code: "vu" },
					{ name: "Javascript", code: "js" },
					{ name: "Open Source", code: "os" }
				]
			}
		},
		methods: {
			validateBeforeSubmit() {
				this.$validator.validateAll().then((result) => {
					if (result) {
						// eslint-disable-next-line
						alert('From Submitted!');
						return;
					}

					alert('Correct them errors!');
				});
			},
			// for selecting and taging
			addTag(newTag) {
				const tag = {
					name: newTag,
					code: newTag.substring(0, 2) + Math.floor(Math.random() * 10000000)
				};
				this.optionsTag.push(tag);
				this.valueTag.push(tag);
			},

			// ---- profile image and email , phone adder
			trigger() {
				this.$refs.onFileChange.click();
			},
			callbackMethod() {
				console.log("salma");
			},
			onFileChange(e) {
				var files = e.target.files || e.dataTransfer.files;
				if (!files.length) {
					return;
				}
				this.createImage(files[0]);
			},
			createImage(file) {
				var reader = new FileReader();
				var vm = this;

				reader.onload = e => {
					vm.image = e.target.result;
				};
				reader.readAsDataURL(file);
			},
			removeImage: function (e) {
				this.image = "";
			},


			// ----- add row input
			addInput(inputScope) {

				if (inputScope == "addEmail") {
					this.emails.push({
						item: ""
					});
				};
				if (inputScope == "addPhone") {
					this.phones.push({
						item: ""
					});
				};
				if (inputScope == "addSocial") {
					this.socials.push({
						item: ""
					});
				};
				if (inputScope == "addLink") {
					this.links.push({
						item: ""
					});
				};
				if (inputScope == "addField") {
					this.addFields.push({
						item: ""
					});
				};
			},

			// remove row input
			deleteInput(inputScope, index) {

				if (inputScope == "deleteEmail") {
					this.emails.splice(index, 1);
				};
				if (inputScope == "deletePhone") {
					this.phones.splice(index, 1);
				};
				if (inputScope == "deleteLink") {
					this.links.splice(index, 1);
				};
				if (inputScope == "deleteSocial") {
					this.socials.splice(index, 1);
				}
				if (inputScope == "deleteField") {
					this.addFields.splice(index, 1);
				}

			},

			// file upload method
			remove(file) {
				this.$refs.upload.remove(file)
			},

			inputFilter(newFile, oldFile, prevent) {
				if (newFile && !oldFile) {
					// Before adding a file
					// 
					// Filter system files or hide files
					//  
					if (/(\/|^)(Thumbs\.db|desktop\.ini|\..+)$/.test(newFile.name)) {
						return prevent()
					}
					// Filter php html js file
					//  php html js 
					if (/\.(php5?|html?|jsx?)$/i.test(newFile.name)) {
						return prevent()
					}
				}
			},
			inputFile(newFile, oldFile) {
				if (newFile && !oldFile) {
					// add
					console.log('add', newFile)
				}
				if (newFile && oldFile) {
					// update
					console.log('update', newFile)
				}
				if (!newFile && oldFile) {
					// remove
					console.log('remove', oldFile)
				}
				// Automatic upload
				if (Boolean(newFile) !== Boolean(oldFile) || oldFile.error !== newFile.error) {
					if (!this.$refs.upload.active) {
						this.$refs.upload.active = true
					}
				}
			}

		}
	};
</script>


