<template>
	<div>
		<b-form @submit.prevent="validateBeforeSubmitEditTalent">
			<div class="row">
				<div class="col-sm-9">
					<div class="row form-group">
						<div class="col-sm-12">
							<b-input v-show="isActive" id="listTitle" type="text" placeholder=" " maxlength="50" name="listTitle" v-model="item.listTitle" value="item.listTitle" v-validate="'required|alpha'" :class="{'input': true, 'border-danger': errors.has('listTitle') }"></b-input>
							<!-- <b-input v-show="isActive" id="inlineInput1" v-model="item.listTitle" type="text" placeholder=" "></b-input> -->
							<strong class="form-control_edit" v-show="!isActive">{{item.listTitle}}</strong>
						</div>
					</div>
					<div class="row form-group">
						<div class="col-sm-12">
							<b-input v-show="isActive" id="inlineInput2" v-model="item.listDescription" type="text" placeholder=""></b-input>
							<span class="form-control_edit" v-show="!isActive">{{item.listDescription}}</span>
						</div>
					</div>

					<div class="form-row">
						<div class="col-sm-12">
							<legend class="col-form-legend cfl-padding">
								<strong>{{item.optionTotalVar}} &nbsp;</strong>
								<span></span>
							</legend>
							<div class="condid-checker">

								<div style="display:inline-block" v-for="checkedName in item.checkedNames">
									<div class="option__condid-img mb-0" v-if="checkedName.img">
										<img style="width:45px" class="option__image rounded-circle" :src="checkedName.img">
									</div>
									<div class="option__condid-img option__condid-img_text mb-0">
										<div class="option__image rounded-circle">
											<span>
												{{checkedName.fname.substring(0,1)}}
											</span>
											<span>
												{{checkedName.lname.substring(0,1)}}
											</span>
										</div>
									</div>
								</div>
								<div class="option__condid-img option__condid-img_text oc-plus mb-0" v-b-modal.talentpool-new-modal-1>
									<div class="option__image rounded-circle">
										<span class="ic_add"></span>
									</div>
								</div>

								<b-modal hide-footer no-fade v-model="showModal" id="talentpool-new-modal-1" class="text-left modal-gray" title=" ">
									<div>
										<b-container>
											<ul class="ul-checkbox">
												<li v-for="itemfix in item.condidate" class="table-box"> 
													<!-- <input type="checkbox"   checked> -->
													<div class="table-box__row">
														<b-form-checkbox class="ul-checkbox__input table-box__cell" v-model="item.checkedNames" :value="item.condidate[itemfix.id]">
															<div class="table-box__cell oci">
																<div class="option__condid-img" v-if="itemfix.img">
																	<img style="width:60px;height:60px;" class="option__image rounded-circle" :src="itemfix.img">
																</div>
																<div class="option__condid-img option__condid-img_text">
																	<div class="option__image rounded-circle rounded-circle_bigsize">
																		<span>{{itemfix.fname.substring(0,1)}}</span>
																		<span>{{itemfix.lname.substring(0,1)}}</span>
																	</div>
																</div>
															</div>
															<div class="option__desc table-box__cell">
																<div class="row">
																	<span class="col">
																		<span class="option__title">{{ itemfix.fname }}</span>
																		<span class="option__small">{{ itemfix.lname }}</span>
																	</span>
																	<span class="option__small col">{{ itemfix.description }}</span>
																</div>
															</div>
														</b-form-checkbox>
													</div>
												</li>
											</ul>
										</b-container>
										<div slot="modal-footer" class="w-100 modal-footer">
											<b-btn size="sm" class="float-right ml-2" variant="success" @click="showModal=false">
											 
											</b-btn>
											<b-btn size="sm" class="float-right btn btn-link" @click="showModal=false">
											
											</b-btn>
										</div>
									</div>
								</b-modal>
								
							</div>
						</div>
						
					</div>
<!-- 

					<b-form-fieldset class="clearfix" v-show="isActive">
						<b-button class="float-left" type="submit" size="sm" variant="primary">Submit</b-button>
					</b-form-fieldset> -->
				</div>
				<div class="col-sm-3 talentpool__multi-button">
					<b-nav class="navbar-nav talentpool__navbar float-right" text="Left align" variant="primary">
						<b-nav-item-dropdown right no-caret>
							<template slot="button-content">
								<i class="ic_setting"></i>
								<span></span>
								<i class="ic_DownArrow ml-2"></i>
							</template>
							<b-dropdown-item @click="editTalent"></b-dropdown-item>
							<b-dropdown-item></b-dropdown-item>
						</b-nav-item-dropdown>
					</b-nav>
					
					<vue-xlsx-table class="talentpool__group-upload float-right" @on-select-file="handleSelectedFile">
						 
					</vue-xlsx-table>

					<div class="clearfix talentpool_position-abs" v-show="isActive">
						<b-button class="float-right" type="submit" size="sm" variant="primary"></b-button>
						<b-button class="float-right btn btn-link mr-2" @click="editTalent" type="reset" size="sm"></b-button>
					</div>
				</div>
			</div>
		</b-form>
	</div>

	
</template>

<script>
// for selecting and taging
import Multiselect from "vue-multiselect";
export default {
	name: "special-item",
	components: {
		// for selecting and taging
		Multiselect,
	},
	props: ['item'],
	data() {
		return {
			showModal:false,
			isActive: false,
			optionTotalVar:'',
			listTitle:''
		};
	},
	methods: {
		validateBeforeSubmitEditTalent() {
			if(this.item.listTitle == ""){
			
			this.$validator.validateAll().then((result) => {
				if (result) {
				// eslint-disable-next-line
				return;
				}
			});
			};
		},
		optionTotal() {
			this.item.optionTotalVar = this.item.condidate.length;
			//alert(this.optionTotalVar)
		},
		editTalent() {
			this.isActive = !this.isActive;
		},
		handleSelectedFile(convertedData) {
			// console.log(convertedData);
			var myObj = convertedData;
			// this.condidate.push(myObj.body[0]);
			var i;

			for (i in myObj.body) {
				this.item.condidate.push(myObj.body[i]);
			}
			this.optionTotal();
		},
	},
	mounted() {
		this.optionTotal();
	}
};
</script>


