<template>
  <div class="example-simple">
    <div class="upload">
      <ul>
        <li  class="mb-3" v-for="(file, index) in files" :key="file.id">

          <div class="row">
            <div class="col-6" >
              <div class="pt-1">{{file.name}}</div>
            </div>
            <div class="col-3">
              <div class="progress mt-2" v-if="file.active || file.progress !== '0.00'">
                <div :class="{'progress-bar': true, 'progress-bar-striped': true, 'bg-danger': file.error, 'progress-bar-animated': file.active}"
                  role="progressbar" :style="{width: file.progress + '%'}">
                  <span>{{file.progress}}%</span>
                  <span v-if="file.error"> - {{file.error}}</span>
                  <span v-else-if="file.success"> - </span>
                </div>
              </div>
            </div>
            <div class="col-3">
              <button class="btn btn-link" type="button" @click.prevent="remove(file)"></button>
            </div>

          </div>

        </li>
      </ul>
    </div>

    <file-upload class="btn btn-primary" post-action="/upload/post" extensions="gif,jpg,jpeg,png,webp" accept="image/png,image/gif,image/jpeg,image/webp"
      :multiple="true" :size="1024 * 1024 * 10" v-model="files" @input-filter="inputFilter" @input-file="inputFile" ref="upload">
      <i class="fa fa-plus"></i>
       
    </file-upload>
  </div>
</template>


<script>
  import FileUpload from 'vue-upload-component'
  export default {
    name: "vue-upload",
    components: {
      FileUpload,
    },
    data() {
      return {
        files: [],
      }
    },
    methods: {
      remove(file) {
        this.$refs.upload.remove(file)
      },

      inputFilter(newFile, oldFile, prevent) {
        if (newFile && !oldFile) {
          // Before adding a file
          // 
          // Filter system files or hide files
          //  
          if (/(\/|^)(Thumbs\.db|desktop\.ini|\..+)$/.test(newFile.name)) {
            return prevent()
          }
          // Filter php html js file
          //  php html js 
          if (/\.(php5?|html?|jsx?)$/i.test(newFile.name)) {
            return prevent()
          }
        }
      },
      inputFile(newFile, oldFile) {
        if (newFile && !oldFile) {
          // add
          console.log('add', newFile)
        }
        if (newFile && oldFile) {
          // update
          console.log('update', newFile)
        }
        if (!newFile && oldFile) {
          // remove
          console.log('remove', oldFile)
        }
        // Automatic upload
        if (Boolean(newFile) !== Boolean(oldFile) || oldFile.error !== newFile.error) {
          if (!this.$refs.upload.active) {
            this.$refs.upload.active = true
          }
        }
      }
    }
  }

</script>




