<template>
	<b-modal  v-model="removeShowModal" id="modalRemove" class="text-left">

		<div class="d-block text-center">
			<h3>  </h3>
		</div>
		<div slot="modal-footer" class="w-100 modal-footer p-2">
			<b-btn size="sm" class="float-right ml-2" variant="danger" @click="removeShowModal=false">
				
			</b-btn>
			<b-btn size="sm" class="float-right" variant="default" @click="removeShowModal=false">
				
			</b-btn>
		</div>

	</b-modal>
</template>
<script>
	export default {
		name: 'modal-remove',
		data() {
			return {
				removeShowModal: false,
			};
		},
		components: {
		},
		methods: {

		}
	}
</script>


