<template>
  <div class="cand-card__main">
    <div class="cand-card__body pt-2">
      <b-tabs class="cand-card__tab" pills>
        <b-tab title="" active>
          <Notes />
        </b-tab>

        <b-tab title="">
          <UserReview />
        </b-tab>

        <b-tab title=" ">
          <Activity />
        </b-tab>
        <b-tab title="">
          <Jobs />
        </b-tab>
        <b-tab title="">
          <InterView />
        </b-tab>
        <b-tab title="">
          <History />
        </b-tab>
      </b-tabs>
    </div>
  </div>
</template>

<style lang="scss">
.cand-card .cand-card__header {
  height: 56px !important;
  background: transparent;
  border-bottom: 0;
  width: 200px;
  padding: 10px;
}
.cand-card .cand-card__main .nav-pills .nav-link.active,
.cand-card .cand-card__main .nav-pills .navbar .active.dropdown-toggle,
.navbar .cand-card .cand-card__main .nav-pills .active.dropdown-toggle {
  border-bottom-color: #fcac00;
}
</style>

<script>
import UserReview from "./userReview";
import CommentReview from "./commentReview";
import Notes from "./notes";
import Activity from "./activity";
import Jobs from "./jobs";
import History from "./history";
import InterView from "./interView";

export default {
  name: "candform-candcard",
  components: {
    // for selecting and taging
    UserReview,
    Activity,
    CommentReview,
    Notes,
    Jobs,
    History,
    InterView,
  },
  data() {
    return {};
  },
  methods: {},
};
</script>


