<template>
  <div class="cand-card__jobs cand-card__tab-page">
    <div class="d-flex align-items-center pb-3">
      <div class="d-flex align-items-center">
        <span class="pl-2"></span>
        <span class="switch-tab">
          <c-switch
            type="text"
            variant="primary-outline-alt"
            on=""
            off=""
            :pill="true"
            :checked="true"
            v-model="status"
          />
        </span>
      </div>
      <div class="mr-auto">
        <button type="button" class="btn float-right ml-2 btn-primary btn-sm">
           
        </button>
      </div>
    </div>
    <b-form-checkbox-group
      id="checkbox-group-1"
      class="cand-card__rate-board"
      v-model="selected"
      name="flavour-1"
    >
      <ul>
        <li v-for="(input, index) in jobsGroup">
          <div
            class="
              cand-card__rate-board-header
              d-flex
              align-items-center
              pt-2
              pb-2
            "
          >
            <span class="option__image rounded-circle rounded-circle_smallsize">
              <img
                src="static/img/avatars/66.jpg"
                class="option__image rounded-circle"
                style="width: 35px; height: 35px"
              />
            </span>
            <span class="mb-0 pr-2 pl-4 d-flex align-items-center"
              >{{ input.title }}
              <i class="inc_keyboard_backspace_black font-24 pr-1 pl-1" />
              <span v-for="(asignItem, i) in input.asign" :key="i"
                >{{ asignItem }}{{ i < input.asign.length - 1 ? " " : "" }}
              </span>
            </span>
            <span class="mr-auto col-auto mr-auto col-auto pl-0">
              <span class="d-flex">
                <span class="mr-2">
                  <i
                    class="inc_info_black"
                    v-show="!isActive"
                    @click="editTalent"
                  />
                </span>
                <span class="mr-2">
                  <i
                    class="inc_delete_forever_black"
                    v-show="!isActive"
                    @click="editTalent"
                  />
                </span>
                <span class="">
                  <i
                    class="inc_close_black"
                    v-show="isActive"
                    @click="editTalent"
                  />
                  <i
                    class="ic_tick mr-2"
                    v-show="isActive"
                    @click="editTalent"
                  />
                  <i
                    class="inc_edit_black"
                    v-show="!isActive"
                    @click="editTalent"
                  />
                </span>
              </span>
            </span>
          </div>

          <div class="user-review-body">
            <b-form-checkbox
              :value="{ ...input, ...{ status: true } }"
              :unchecked-value="input"
              ><span class="pr-2">{{ input.label }}</span></b-form-checkbox
            >
          </div>
        </li>
      </ul>
    </b-form-checkbox-group>
  </div>
</template>
<style lang="scss">
.cand-card__rate-board-header {
  background: #f9fafa;
  * {
    color: #393e7a;
  }
  i {
    color: #7b7b7b;
  }
}

.cand-card__jobs {
  input[type="checkbox"]:checked + span + span {
    text-decoration: line-through;
  }
  .custom-control.custom-checkbox {
    display: flex;
    align-items: center;
  }
}
</style>
<script>
import cSwitch from "../../../components/Switch";

export default {
  name: "Jobs",
  components: {
    cSwitch,
  },
  data() {
    return {
      status: true,
      selected: [
        {
          title: " ",
          label: "         ",
          asign: [" ", ""],
          id: 1,
          status: true,
        },
      ],
      jobsGroup: [
        {
          title: " ",
          asign: [" ", ""],
          label: "         ",
          id: 0,
          status: false,
        },
        {
          title: " ",
          label: "         ",
          asign: [" ", ""],
          id: 1,
          status: false,
        },
        {
          title: " ",
          label: "         ",
          asign: [" ", ""],
          id: 1,
          status: false,
        },
        {
          title: " ",
          label: "         ",
          asign: [" ", ""],
          id: 1,
          status: false,
        },
        {
          title: " ",
          label: "         ",
          asign: [" ", ""],
          id: 1,
          status: false,
        },
      ],
      isActive: false,
    };
  },

  methods: {
    editTalent() {
      this.isActive = !this.isActive;
    },
  },
};
</script>


