<template>
  <div class="cand-card user-profile company-profile mb-5">
    <!-- <div class="profile-form mb-3 p-4 border-full">
      <div class="user-profile__header clearfix">
        <h4>company profile</h4>
      </div>
    </div> -->

    <div class="row">
      <div class="col-lg-6">
        <div class="cand-card__main user-profile__user-info">
          <div class="cand-card__body">
            <div class="company-profile-info">
              <b-form @submit.prevent="validateBeforeSubmit">
                <div class="row">
                  <fieldset class="b-form-group form-group col-lg-6">
                    <div class="form-row">
                      <legend class="col-form-legend col-sm-5">
                        <i class='ic_MyCompany mr-1'></i>
                        <span> </span>
                      </legend>
                      <div class="col-sm-7">
                        <b-input id="companyNameFa" v-model="companyNameFa" type="text" maxlength="30"></b-input>
                      </div>
                    </div>
                  </fieldset>
                  <fieldset class="b-form-group form-group col-lg-6">
                    <div class="form-row">
                      <div class="col-sm-12">
                        <b-input id="companyNameEn" v-model="companyNameEn" type="text" maxlength="30"></b-input>
                      </div>
                    </div>
                  </fieldset>
                </div>
                <div class="row">
                  <fieldset class="b-form-group form-group col-lg-6">
                    <div class="form-row">
                      <legend class="col-form-legend col-sm-5">
                        <i class='ic_email mr-1'></i>
                        <span></span>
                      </legend>
                      <div class="col-sm-7">
                        <!-- <b-input id="email-input" type="email" v-model="companyEmail" placeholder="enter password" maxlength="30"></b-input> -->
                        <b-form-input id="email-input" type="text" v-model="companyEmail" placeholder="  " class="float-left w-50"></b-form-input>
                        <span class="w-50 pl-1 company-profile__static-input">@gmail.com</span>
                      </div>
                    </div>
                  </fieldset>

                  <fieldset class="b-form-group form-group  col-lg-6">
                    <div class="form-row">
                      <legend class="col-form-legend col-sm-5">
                        <i class='ic_web mr-1'></i>
                        <span> </span>
                      </legend>
                      <div class="col-sm-7">
                        <b-input id="companyWebsite" v-model="companyWebsite" type="text" maxlength="30"></b-input>
                      </div>
                    </div>
                  </fieldset>

                  <fieldset class="b-form-group form-group  col-lg-6">
                    <div class="form-row">
                      <legend class="col-form-legend col-sm-5">
                        <i class='ic_web mr-1'></i>
                        <span> </span>
                      </legend>
                      <div class="col-sm-7">
                          <b-form-input id="companySpecialLink" v-model="companySpecialLink" type="text" maxlength="30" placeholder="  " class="float-left w-50" readonly></b-form-input>
                          <span class="w-50 pl-1 company-profile__static-input">.icon.com</span>
                        <!-- <b-input id="companySpecialLink" v-model="companySpecialLink" type="text" maxlength="30"></b-input> -->
                      </div>
                    </div>
                  </fieldset>
                </div>

                <b-form-fieldset label="" :label-cols="3" :horizontal="false" class="profile-form_border-desc">
                  <b-form-textarea type="text" v-model="companyDescription" :rows="5" :max-row="5" placeholder="  " maxlength="1000"></b-form-textarea>
                </b-form-fieldset>


                <div class="row">


                  <div class="company-profile__header img-avatar col-md-6">
                    <div class="img-avatar_less">
                      <button type="button" class="btn tb__default-btn btn-sm" @click="trigger">
                        <i class="profile-avatar__icon ic_add mr-1"></i>
                        <span>  </span>
                      </button>
                      <span v-if="image">
                        <span class="pt-2 pb-2 ml-3 mr-3 border-bottom">{{imageHeaderName}}</span>
                        <i @click="removeImage" class="ic_delete img-avatar_remove"></i>
                      </span>
                      <input class="sr-only" type="file" name="header-image" @change="onFileChange" ref="onFileChange">
                    </div>
                    <!-- <img :src="image" class="sr-only" /> -->
                  </div>

                  <div class="company-profile__color-picker col-md-6">
                    <legend class="col-form-legend">
                      <span>   </span>
                      <i class="icon ml-3">
                        <img src="data:image/jpeg;base64,">
                      </i>
                        <b-dropdown right>
                            <b-dropdown-header tag="div" class="text-center">
        
                              <chrome-picker :value="colors" @input="updateValue"></chrome-picker>
                            </b-dropdown-header>


                       
                        </b-dropdown>
                    </legend>
                  </div>

                  <div class="company-profile__logo img-avatar col-md-6">
                    <div class="img-avatar_less">
                      <button type="button" class="btn tb__default-btn btn-sm" @click="triggerLogo">
                        <i class="profile-avatar__icon ic_add"></i>
                        <span> </span>
                      </button>
                      <span v-if="imageLogo">
                        <span class="pt-2 pb-2 ml-3 mr-3 border-bottom">{{imageLogoName}}</span>
                        <i @click="removeImageLogo" class="ic_delete img-avatar_remove"></i>
                      </span>
                      <input class="sr-only" type="file" name="logo-image" @change="onFileChangeLogo" ref="onFileChangeLogo">
                    </div>
                    <img :src="imageLogo" class="sr-only" />
                  </div>

                </div>

                <div slot="footer" class="text-right">

                  <button type="submit" class="btn-footer btn btn-primary btn-sm"></button>
                  <a :href="companyWebsite" target="_blank" class="btn-footer btn btn-default ml-2" size="sm"></a>
                  <!-- <b-button type="button" size="sm" variant="success" class="ml-1">file upload</b-button> -->
                </div>

              </b-form>
            </div>
          </div>
        </div>
      </div>

      <div class="col-lg-6 hidden-md-down">
        <div class="cand-card__main user-profile__activity">
          <div class="cand-card__body p-0 pb-3">
            <div class="company-page">
              <div class="company-page__header-top-bar" :style="{'background-color': bgc}">
                <span>{{companySpecialLink}}</span>
                <span>.logo.com</span>
              </div>
              <div class="company-page__header-background text-center" :style="{backgroundImage:'url('+image+')'}">
                <!-- <img :src="image" alt=""> -->
                <span class="company-page__header-text" v-if="!image">
                  <i class="ic_image company-page__header-text-icon"></i>
                  <span class="company-page__header-text-dest">COVER IMAGE</span>
                  <span class="company-page__header-text-subdest">jpeg,png</span>
                </span>
              </div>
              <div class="company-page__header-logo" :style="{backgroundImage:'url('+imageLogo+')'}">
                <span class="company-page__header-text" v-if="!imageLogo">
                  <i class="ic_image company-page__header-text-icon"></i>
                </span>
              </div>
              <div class="company-page__body-content">
                <div class="company-page__description">
                  <span class="company-page__title mb-3">
                    {{companyNameEn}}
                  </span>
                  <p class="company-page__body">
                    {{companyDescription}}
                  </p>
                </div>
              </div>

              <div class="company-page__activity-box">
                <span class="company-page__title mb-3">
                   
                </span>
                <div class="user-profile__activity-item">
                  <div class="user-profile__table">
                    <div class="user-profile__table-row">

                      <div class="user-profile__table-td">
                        <div class="mb-2">
                          <span class="user-profile__title mr-3"> </span>
                          <span class="user-profile__email-link">
                            <span class="mr-2"> : </span>
                            <span>design001@logo.com</span>
                          </span>
                        </div>

                        <div class="">
                          <span class="mr-3">,</span>
                          <span class="mr-3">
                            <i class="ic_activity"></i>
                            <span>20  </span>
                          </span>
                          <span class="mr-3 user-profile__email-link">
                            <span class="mr-2"> :</span>
                            <span>shetaab.logo.com/jobs/001</span>
                          </span>
                        </div>

                      </div>

                      <div class="user-profile__multi-button user-profile__table-td text-right">
                        <div class="user-profile_position-abs">
                          <b-button class="" type="submit" size="sm" variant="primary"> </b-button>
                        </div>
                      </div>

                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

    </div>
    <modalRemove></modalRemove>
  </div>
</template>

<script>
  import modalRemove from "../components/modalRemove"
  import { Chrome } from 'vue-color';

  let defaultProps = {
    hex: '#0083eb',
  }
  export default {
    name: "candform-candcard",
    components: {
      // for selecting and taging
      modalRemove,
      'chromePicker': Chrome,


    },
    data() {
      return {
        image: "",
        imageHeaderName: "",
        imageLogo: "",
        imageLogoName: "",
        companyEmail: 'radman',
        companyWebsite: 'http://google.com',
        companySpecialLink: 'raman',
        companyDescription: 'Lorem Ipsum Dolor Sit Amet Consectetur Adipisicing Elit. Ducimus, Velit Voluptas Quam Amet Esse Aliquam Maxime Cum Consectetur Nemo Tempora Praesentium Facere Minima Minus Aliquid Possimus Quasi? Nisi, Officia Aspernatur.',
        email: '',
        companyNameFa: '  ',
        companyNameEn: 'Radman group',
        colorPicker: false,
        colors: defaultProps
      }
    },
    computed: {
      bgc() {
        return this.colors.hex
      }
    },
    methods: {
      validateBeforeSubmit() {
        this.$validator.validateAll().then((result) => {
          if (result) {
            // eslint-disable-next-line
            alert('From Submitted!');
            return;
          }

          alert('Correct them errors!');
        });
      },

      //color picker -----
      onOk() {
        console.log('ok')
      },
      onCancel() {
        console.log('cancel')
      },
      updateValue(value) {
        this.colors = value
      },



      // upload image
      onFileChange(e) {

        var files = e.target.files || e.dataTransfer.files;
        if (!files.length) {
          return;
        }
        this.createImage(files[0]);
      },
      trigger() {
        this.$refs.onFileChange.click();
      },
      createImage(file) {
        var reader = new FileReader();
        var vm = this;

        reader.onload = e => {
          vm.image = e.target.result;
        };
        reader.readAsDataURL(file);
        this.imageHeaderName = file.name;

      },
      removeImage: function (e) {
        this.image = "";
      },

      //upload logo
      onFileChangeLogo(e) {

        var filesLogo = e.target.files || e.dataTransfer.files;
        if (!filesLogo.length) {
          return;
        }
        this.createImageLogo(filesLogo[0]);
      },
      triggerLogo() {
        this.$refs.onFileChangeLogo.click();
      },
      createImageLogo(file2) {
        var reader = new FileReader();
        var vm = this;
        reader.onload = e => {
          vm.imageLogo = e.target.result;
        };
        reader.readAsDataURL(file2);
        this.imageLogoName = file2.name;
      },
      removeImageLogo: function (e) {
        this.imageLogo = "";
        this.imageLogoName = "";
      },
    }

  }

</script>


