<template>
  <div class="cand-card__ndoes cand-card__tab-page">
    <b-form>
      <b-form-fieldset>
        <b-form-textarea
          type="text"
          :rows="3"
          :max-row="6"
          placeholder="        "
          maxlength="1000"
        ></b-form-textarea>
      </b-form-fieldset>
      <div class="clearfix">
        <div class="float-right d-flex align-items-center">
          <span
            class="d-flex align-items-center justify-content-center"
            @click="privatePostToggle"
          >
            <i class="inc_lock_open_black private-icon" v-show="!privatePost" />
            <i
              class="inc_lock_black private-icon private-on"
              v-show="privatePost"
            />
          </span>
          <button type="submit" class="btn ml-2 btn-primary btn-sm"></button>
        </div>
      </div>
    </b-form>

    <div class="d-flex align-items-center pb-3">
      <div class="d-flex align-items-center">
        <span class="pl-2"></span>
        <span class="switch-tab">
          <c-switch
            type="text"
            variant="primary-outline-alt"
            on=""
            off=""
            :pill="true"
            :checked="true"
            v-model="status"
          />
        </span>
      </div>
    </div>
    <CommentReview />
  </div>
</template>
<style lang="scss">
.private-icon {
  font-size: 25px;
  color: #a8a4a4;
  &.private-on {
    color: #393e7a;
  }
}
</style>
<script>
import CommentReview from "./commentReview";
import cSwitch from "../../../components/Switch";

export default {
  name: "CandidateNotes",
  components: {
    CommentReview,
    cSwitch,
  },
  data() {
    return {
      status: true,
      privatePost: false,
    };
  },
  methods: {
    privatePostToggle() {
      this.privatePost = !this.privatePost;
    },
  },
};
</script>


