<template>
  <div class="cand-card user-profile mb-5">
    <div class="profile-form mb-3 p-4 border-full">
      <div class="user-profile__header clearfix">

        <div class="float-left">
          <div class="profile-avatar img-avatar">
            <div v-if="!image" class="img-avatar_less" @click="trigger">
              <i class="profile-avatar__icon ic_image"></i>
              <input type="file" @change="onFileChange" ref="onFileChange">
            </div>

            <div v-else>
              <div class="img-avatar_size img-avatar">
                <img :src="image" />
              </div>
              <i @click="removeImage" class="ic_edit img-avatar_remove"></i>
            </div>
          </div>
        </div>

        <div class="float-right cand-card__user-detail">
          <div class="cand-card__full-name-edit clearfix">
            <span class="">
              <span class="mr-5 cand-card__name" v-show="!fullNameShowBoo">{{firstName_user+" "+lastName_user}}</span>
              <span class="cand-card__name cand-card_inline" v-show="fullNameShowBoo">
                <b-input id="firstName_user" type="text" maxlength="30" name="firstName_user" class="" v-model="firstName_user"></b-input>
                <b-input id="lastName_user" type="text" maxlength="30" name="lastName_user" class="" v-model="lastName_user"></b-input>
              </span>
              <i class="ic_edit mr-2" @click="fullNameShow"></i>
              <i class="ic_delete" v-b-modal.modalRemove></i>
            </span>
          </div>
          <div class="cand-card__addfrom mb-1">
            <span>  </span>
          </div>
        </div>

      </div>
    </div>

    <div class="clearfix">

		<div class="w-50 float-left cand-card__main user-profile__user-info">
		<div class="user-profile__main-title">
			<strong></strong>
		</div>
        <div class="cand-card__body">
          <userInformation></userInformation>
        </div>
      </div>

      <div class="w-50 float-right cand-card__main user-profile__activity">
		<div class="user-profile__main-title">
			<strong> </strong>
		</div>
        <div class="cand-card__body">
          <userActivity></userActivity>
        </div>
      </div>

    </div>
    <modalRemove></modalRemove>
  </div>
</template>

<script>
  import userInformation from "./userInformation";
  import userActivity from "./userActivity";
  import modalRemove from "../components/modalRemove"
  export default {
    name: "candform-candcard",
    components: {
      // for selecting and taging
      modalRemove,
      userInformation,
      userActivity
    },
    data() {
      return {
        image: "",
        email: 'm.rastegar991@gmail.com',
        phone: '09358537536',
        social: "linkedin.com/m.rastegar",
        link: 'test1,test2,test3',
        firstName_user: '',
        lastName_user: '',
        source: ' ',
        isActive: false,
        fullNameShowBoo: false,
      }
    },
    methods: {
      validateBeforeSubmit() {
        this.$validator.validateAll().then((result) => {
          if (result) {
            // eslint-disable-next-line
            alert('From Submitted!');
            return;
          }

          alert('Correct them errors!');
        });
      },
      fullNameShow() {
        this.fullNameShowBoo = !this.fullNameShowBoo;
      },

      // for selecting and taging

      // ---- profile image and email , phone adder
      trigger() {
        this.$refs.onFileChange.click();
      },
      callbackMethod() {
        console.log("salma");
      },
      onFileChange(e) {
        var files = e.target.files || e.dataTransfer.files;
        if (!files.length) {
          return;
        }
        this.createImage(files[0]);
      },
      createImage(file) {
        var reader = new FileReader();
        var vm = this;

        reader.onload = e => {
          vm.image = e.target.result;
        };
        reader.readAsDataURL(file);
      },
      removeImage: function (e) {
        this.image = "";
      },


    },
  }

</script>



