
<template>
  <b-tooltip :target="target" placement="bottom" :class="[target]">
    <div
      class="tooltip-wrapper d-flex align-items-center flex-column justify-content-round"
    >
      <div
        v-for="(input, index) in data"
        :key="index"
        :class="[
          'w-100 d-flex align-items-center',
          {
            'pb-1': index !== data.length - 1,
          },
        ]"
      >
        <span>{{ input.title }}</span>
        <span class="mr-auto">{{ input.description }}</span>
      </div>
    </div>
  </b-tooltip>
</template>
<style lang="scss">
.tooltip {
  .arrow {
    margin-left: 13px;
    border-color: #fff;
    display: none;
  }
  .tooltip-inner {
    width: 139px;
    padding: 8px 10px 9px 8px;
    box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.47);
    background-color: #ffffff;
    color: #a8a4a4;
  }
  // &.bs-tooltip-top .arrow::before,
  // &.bs-tooltip-auto[x-placement^="top"] .arrow::before {
  //   left: -15px;
  //   content: "";
  //   border-width: 10px 15px 0;
  //   border-top-color: #fff;
  //   box-shadow: 0 -3px 6px 0 rgba(0, 0, 0, 0.47);
  // }
}
</style>
<script>
export default {
  name: "Tooltip",
  props: ["target", "data"],

  data() {},
  methods: {},
};
</script>


