<template>
    <div class="profile-form">
        <b-form @submit.prevent="validateBeforeSubmit">
            <div class="row form-group">

                <div class="col-sm-3">
                    <div class="profile-avatar img-avatar">
                        <div v-if="!image" class="img-avatar_less" @click="trigger">
                            <i class="profile-avatar__icon ic_image"></i>
                            <input type="file" @change="onFileChange" ref="onFileChange">
                        </div>

                        <div v-else>
                            <div class="img-avatar_size img-avatar">
                                <img :src="image" />
                            </div>
                            <i @click="removeImage" class="ic_edit img-avatar_remove"></i>
                        </div>
                    </div>
                </div>

                <div class="col-sm-9">
                    <div class="row">
                        <div class="col">
                            <b-input id="firstName_candform" type="text" maxlength="30" name="firstName_candform" class="" value=" " disabled></b-input>
                        </div>
                    </div>
                </div>

            </div>

            <fieldset class="b-form-group form-group">
                <div class="form-row">
                    <legend class="col-form-legend col-sm-3">
                        <i class='ic_jobs mr-1'></i>
                        <span>/</span>
                    </legend>
                    <div class="col-sm-9">
                        <multiselect v-model="value" :options="options" :multiple="true" :close-on-select="false" :clear-on-select="false" :hide-selected="true" :preserve-search="true" placeholder="Pick some" label="name" track-by="name">
                            <template slot="tag" slot-scope="props">
                                <span class="multiselect__tag">
                                    <span>{{ props.option.language }}</span>
                                    <i aria-hidden="true" tabindex="1" class="multiselect__tag-icon" @click="props.remove(props.option)"></i>
                                </span>
                            </template>
                        </multiselect>
                    </div>
                </div>
            </fieldset>

        
            <fieldset class="b-form-group form-group">
                <div class="form-row">
                    <legend class="col-form-legend col-sm-3">
                        <i class='ic_tags mr-1'></i>
                        <span> </span>
                    </legend>
                    <div class="col-sm-9">
                        <multiselect v-model="valueTag" tag-placeholder="   " placeholder="   " label="name" track-by="code"
                        :options="optionsTag" :multiple="true" :taggable="true" @tag="addTag"></multiselect>
                    </div>
                </div>
            </fieldset>

        <div class="dynamic-input">

            <b-form-fieldset label="<i class='ic_email mr-1'></i> " :label-cols="3" :horizontal="true">
                <b-form-input name="email" v-model="email" v-validate="'required|email'" :class="{'input': true, 'border-danger': errors.has('email') }" placeholder="   " maxlength="100"></b-form-input>
                <span v-show="errors.has('email')" class="help-valid text-danger">this field required</span>
                <div class="position-relative" v-for="(input, index) in emails">
                    <b-form-input type="email" placeholder="enter Email" v-model="input.item" maxlength="100"></b-form-input>
                    <i class="ic_close dynamic-input_remover" @click="deleteInput('deleteEmail',index)"></i>
                </div>

                <i class="ic_add dynamic-input_adder" @click="addInput('addEmail')" v-if="emails.length < 1"></i>
            </b-form-fieldset>

            <b-form-fieldset label="<i class='ic_phone mr-1'></i> " :label-cols="3" :horizontal="true">
                <b-form-input name="phone" v-model="phone" v-validate="'numeric'" :class="{'input': true, 'border-danger': errors.has('phone') }" type="text" placeholder="  " maxlength="30"></b-form-input>
                <span v-show="errors.has('phone')" class="help-valid text-danger">this field required</span>
                <div class="position-relative" v-for="(input, index) in phones">
                    <b-form-input type="text" placeholder="  " v-model="input.item" maxlength="30"></b-form-input>
                    <i class="ic_close dynamic-input_remover" @click="deleteInput('deletePhone',index)"></i>
                </div>
                <i class="ic_add dynamic-input_adder" @click="addInput('addPhone')" v-if="phones.length < 1"></i>
            </b-form-fieldset>



            <b-form-fieldset v-for="(input,label, index) in addFields">
                <div class="form-row">
                    <legend class="col-form-legend col-sm-3">
                        <b-form-input type="text" placeholder="" v-model="input.label" maxlength="200"></b-form-input>
                    </legend>
                    <div class="col-form-legend col-sm-9"> 
                        <div class="position-relative">
                            <b-form-input type="text" placeholder="" v-model="input.item" maxlength="200"></b-form-input>
                            <i class="ic_close dynamic-input_remover" @click="deleteInput('deleteField',index)"></i>
                        </div>
                    </div>
                </div>
            </b-form-fieldset>

        </div>

        <div role="tablist" class="tablist-accordion">
            <b-card no-body>
                <b-card-header header-tag="header" class="p-1" role="tab">
                    <span class="accordion-title" block href="#" v-b-toggle.accordion1>  <span class="accordion-title__select"></span></span>
                </b-card-header>
                <b-collapse id="accordion1" accordion="my-accordion" role="tabpanel">
                    <b-card-body>
                        <b-form-fieldset label="<i class='ic_social mr-1'></i>  " :label-cols="3" :horizontal="true">
                            <b-form-input type="text" placeholder="   "></b-form-input>
                            <div class="position-relative" v-for="(input, index) in socials">
                                <b-form-input type="text" placeholder="   " v-model="input.item"></b-form-input>
                                <i class="ic_close dynamic-input_remover" @click="deleteInput('deleteSocial',index)"></i>
                            </div>
                            <i class="ic_add dynamic-input_adder" @click="addInput('addSocial')"></i>
                        </b-form-fieldset>

                        <b-form-fieldset label="<i class='ic_link mr-1'></i> " :label-cols="3" :horizontal="true">
                            <b-form-input type="text" placeholder="  "  maxlength="300"></b-form-input>
                            <div class="position-relative" v-for="(input, index) in links">
                                <b-form-input type="text" placeholder="  " v-model="input.item"  maxlength="300"></b-form-input>
                                <i class="ic_close dynamic-input_remover" @click="deleteInput('deleteLink',index)"></i>
                            </div>
                            <i class="ic_add dynamic-input_adder" @click="addInput('addLink')" v-if="links.length < 8"></i>
                        </b-form-fieldset>
            
                    </b-card-body>
                </b-collapse>
            </b-card>
        </div>
        <b-form-fieldset label="" :label-cols="3" :horizontal="false">
            <b-form-textarea type="text" :rows="3" :max-row="6" placeholder="  " maxlength="1000"></b-form-textarea>
        </b-form-fieldset>

        <div slot="footer">
            <b-button type="submit" size="sm" variant="primary"></b-button>
        </div>
        </b-form>
    </div>
</template>

<script>
	// for selecting and taging
	import Multiselect from "vue-multiselect";
	export default {
        name: "candform-applicat",
		components: {
			// for selecting and taging
			Multiselect
		},
		data() {
			return {
				image: "",
				emails: [],
				phones: [],
				socials: [],
                links: [],
                firstName_candform:'',
                lastName:'',
                email:'',
                phone:'',
                addFields:[],
                

				// for selecting and taging
				value: [
					{ name: 'Vue.js', language: 'JavaScript' }
				],
				options: [
					{ name: 'Vue.js', language: 'JavaScript' },
					{ name: 'Adonis', language: 'JavaScript' },
					{ name: 'Rails', language: 'Ruby' },
					{ name: 'Sinatra', language: 'Ruby' },
					{ name: 'Laravel', language: 'PHP' },
					{ name: 'Phoenix', language: 'Elixir' }
				],
				valueTag: [{ name: "Javascript", code: "js" }],
				optionsTag: [
					{ name: "Vue.js", code: "vu" },
					{ name: "Javascript", code: "js" },
					{ name: "Open Source", code: "os" }
				]
			}
		},
		methods: {
            validateBeforeSubmit() {
                this.$validator.validateAll().then((result) => {
                    if (result) {
                    // eslint-disable-next-line
                    alert('From Submitted!');
                    return;
                    }

                    alert('Correct them errors!');
                });
            },
			// for selecting and taging
			addTag(newTag) {
				const tag = {
					name: newTag,
					code: newTag.substring(0, 2) + Math.floor(Math.random() * 10000000)
				};
				this.optionsTag.push(tag);
				this.valueTag.push(tag);
			},


			// ---- profile image and email , phone adder
			trigger() {
				this.$refs.onFileChange.click();
			},
			callbackMethod() {
				console.log("salma");
			},
			onFileChange(e) {
				var files = e.target.files || e.dataTransfer.files;
				if (!files.length) {
					return;
				}
				this.createImage(files[0]);
			},
			createImage(file) {
				var reader = new FileReader();
				var vm = this;

				reader.onload = e => {
					vm.image = e.target.result;
				};
				reader.readAsDataURL(file);
			},
			removeImage: function (e) {
				this.image = "";
			},


			// ----- add row input
			addInput(inputScope){

				if(inputScope == "addEmail") {
					this.emails.push({
						item: ""
					});
				};
				if(inputScope == "addPhone") {
					this.phones.push({
						item: ""
					});
				};
				if(inputScope == "addSocial") {
					this.socials.push({
						item: ""
					});
				};
				if(inputScope == "addLink") {
					this.links.push({
						item: ""
					});
				};
				if(inputScope == "addField") {
					this.addFields.push({
						item: ""
					});
				};
			},

			// remove row input
			deleteInput(inputScope,index){

				if(inputScope == "deleteEmail") {
					this.emails.splice(index, 1);
				};
				if(inputScope == "deletePhone") {
					this.phones.splice(index, 1);
				};
				if(inputScope == "deleteLink") {
					this.links.splice(index, 1);
				};
				if(inputScope == "deleteSocial") {
					this.socials.splice(index, 1);
				}
				if(inputScope == "deleteField") {
					this.addFields.splice(index, 1);
				}

			}

		}
	};
</script>


