<template>
  <div class="cand-card__main">
    <div class="cand-card__body pt-2">
      <b-tabs class="cand-card__tab" pills>
        <b-tab title=""> <Resume /> </b-tab>

        <b-tab title="">
          <Documents />
          <!-- <vueUpload /> -->
        </b-tab>
        <b-tab title=""> <Questionnaire /> </b-tab>
        <b-tab title="  " active> <FurtherInfo /> </b-tab>
      </b-tabs>
    </div>
  </div>
</template>
<style lang="scss">
.cand-card {
  a {
    color: #393e7a;
  }
  input,
  textarea {
    &.form-control {
      margin: 4px 0 7px 0;
      padding: 11px 13px;
      border-radius: 2px;
      border: solid 1px #a8a4a4 !important;
    }
  }
  .form-group {
    margin-bottom: 0px;
  }
  input.form-control {
    height: 30px;
  }
  textarea.form-control {
    height: auto;
  }
  .multi-tag {
    .form-control {
      border-left: 0 !important;
      border-top: 0 !important;
      border-right: 0 !important;
      padding: 0;
    }
  }
  button[type="reset"],
  button[type="submit"],
  .modal-footer .btn,
  .btn.btn-success,
  .btn.btn-default {
    min-width: 56px;
    height: 30px;
    padding: 5px 18px;
    border-radius: 2px;
    background-color: #393e7a;
    overflow: hidden;
    font-weight: normal;
    font-size: 12px;
  }
  .font-24 {
    font-size: 24px !important;
  }

  .custom-circle-select {
    &.switch-primary-outline-alt > .switch-input:checked {
      ~ .switch-label {
        border-color: #bebebe;
      }
      ~ .switch-handle {
        background: #393e7a !important;
        border-color: #393e7a;
      }
    }
  }
}

.cand-card .cand-card__rate-board {
  max-height: 100%;
  color: #7b7b7b;
  i {
    color: #7b7b7b;
    font-size: 16px;
  }
}
.tb__comment-box .card-body,
.tb__comment-box .card-block,
.tb__comment-box .card-block {
  margin-left: auto;
  margin-right: auto;
  width: 100%;
}
.cand-card .cand-card__main {
  border: 0;
  &.profile-form {
    position: absolute;
    top: -56px;
    background: white;
    right: 1px;
    border-radius: 30% 0% 0% 0% / 100% 0% 0% 0%;
  }
  .cand-card__body {
    background: white;
    display: flex;
    flex-direction: column;
    flex: 1;
    height: auto;
  }
  .cand-card__tab {
    flex: 1;
    top: 0;
    display: flex;
    flex-direction: column;
    .tab-content {
      flex: 1;
    }
  }

  .tab-content .tab-pane {
    padding: 1rem !important;
    position: relative;
    height: 100%;
  }

  .switch.switch-icon {
    margin-bottom: 0;
    vertical-align: middle;
  }
}
</style>
<script>
import vueUpload from "../../components/VueUpload";
import Documents from "./documents";
import Questionnaire from "./questionnaire";
import FurtherInfo from "./furtherInfo";
import Resume from "./resume";

export default {
  name: "CandidateDetailsSide",
  components: {
    // for selecting and taging
    vueUpload,
    Documents,
    FurtherInfo,
    Questionnaire,
    Resume,
  },
  data() {
    return {};
  },
  methods: {
    // ---- profile image and email , phone adder
    trigger() {
      this.$refs.onFileChange.click();
    },
    onFileChange(e) {
      var files = e.target.files || e.dataTransfer.files;
      if (!files.length) {
        return;
      }
      this.createImage(files[0]);
    },
    createImage(file) {
      var reader = new FileReader();
      var vm = this;

      reader.onload = (e) => {
        vm.image = e.target.result;
      };
      reader.readAsDataURL(file);
    },
    removeImage: function (e) {
      this.image = "";
    },
  },
};
</script>


