<template>
  <ul class="cand-card__rate-board">
    <li v-for="(input, index) in reviewGroup" :key="index">
      <div
        class="cand-card__rate-board-header d-flex align-items-center pt-2 pb-2"
      >
        <span class="option__image rounded-circle rounded-circle_smallsize">
          <img
            src="static/img/avatars/66.jpg"
            class="option__image rounded-circle"
            style="width: 35px; height: 35px"
          />
        </span>
        <span class="mb-0 pr-2 pl-4">  </span>
        <span class="mr-auto col-auto pl-0">
          <span class="d-flex">
            <span class="mr-2">
              <i
                class="inc_info_black"
                v-show="!isActive"
                @click="editTalent"
              />
            </span>
            <span class="mr-2">
              <i
                class="inc_delete_forever_black"
                v-show="!isActive"
                @click="editTalent"
              />
            </span>
            <span class="">
              <i
                class="inc_close_black"
                v-show="isActive"
                @click="editTalent"
              />
              <i class="ic_tick mr-2" v-show="isActive" @click="editTalent" />
              <i
                class="inc_edit_black"
                v-show="!isActive"
                @click="editTalent"
              />
            </span>
          </span>
        </span>
      </div>

      <div class="user-review-body">
        <p class="mt-1">
          Lorem, ipsum dolor sit amet consectetur adipisicing elit. Quis
          recusandae, quidem voluptates libero eveniet expedita minima
          laudantium? Voluptatem ut sapiente illo velit, architecto non vel quas
          officiis facilis iste? Iure.
        </p>
      </div>
    </li>
  </ul>
</template>


<style lang="scss">
</style>

<script>
export default {
  name: "CandidateCardCommentReview",
  data() {
    return {
      isActive: false,
      reviewGroup: [1, 2, 3, 4, 5],
    };
  },
  methods: {
    editTalent() {
      this.isActive = !this.isActive;
    },
  },
};
</script>


