<template>
  <div class="cand-card__body-info d-flex flex-column">
    <div class="cand-card__body-info_part-1">
      <div class="cand-card__edit-field form-group">
        <b-form @submit.prevent="validateBeforeSubmit" class="absolute-part">
          <div class="form-group d-flex pb-3 pl-4">
            <span class="cand-card__body-info__main-title"></span>
            <div class="mr-auto pl-2 align-items-center d-flex">
              <i class="ic_delete mr-2" v-show="isActive" @click="editTalent" />
              <i class="ic_tick" v-show="isActive" @click="editTalent" />
              <i
                class="inc_edit_black"
                v-show="!isActive"
                @click="editTalent"
              />
            </div>
          </div>

          <div class="field-group bg-white overflow-y-auto">
            <b-form-fieldset
              label="<i class='inc_mail_outline_black mr-1' />"
              :label-cols="1"
              :horizontal="true"
            >
              <b-form-input
                v-show="isActive"
                name="email"
                v-model="email"
                v-validate="'required|email'"
                :class="{ input: true, 'border-danger': errors.has('email') }"
                placeholder="   "
                maxlength="100"
              ></b-form-input>
              <span v-show="errors.has('email')" class="help-valid text-danger"
                >this field required</span
              >
              <span class="form-control_edit" v-show="!isActive">{{
                email
              }}</span>
            </b-form-fieldset>

            <b-form-fieldset
              label="<i class='inc_phone_black mr-1'></i>"
              :label-cols="1"
              :horizontal="true"
            >
              <b-form-input
                v-show="isActive"
                name="phone"
                v-model="phone"
                v-validate="'numeric'"
                :class="{ input: true, 'border-danger': errors.has('phone') }"
                type="text"
                placeholder="  "
                maxlength="30"
              ></b-form-input>
              <span v-show="errors.has('phone')" class="help-valid text-danger"
                >this field required</span
              >

              <span class="form-control_edit" v-show="!isActive">{{
                phone
              }}</span>
            </b-form-fieldset>

            <b-form-fieldset
              label="<i class='inc_link_black mr-1'></i>"
              :label-cols="1"
              :horizontal="true"
            >
              <b-form-input
                v-show="isActive"
                type="text"
                v-model="social"
                placeholder="   "
              ></b-form-input>
              <span class="form-control_edit" v-show="!isActive">{{
                social
              }}</span>
            </b-form-fieldset>

            <b-form-fieldset
              label="<i class='inc_get_app_black mr-1'></i>"
              :label-cols="1"
              :horizontal="true"
            >
              <b-form-input
                v-show="isActive"
                type="text"
                v-model="link"
                placeholder="  "
                maxlength="300"
              ></b-form-input>
              <span class="form-control_edit" v-show="!isActive">{{
                link
              }}</span>
            </b-form-fieldset>
            <fieldset class="b-form-group form-group">
              <div class="form-row">
                <legend class="col-form-legend col-sm-1">
                  <i class="inc_people_black mr-1"></i>
                </legend>
                <div class="col-sm-11">
                  <span class="form-control_edit">{{ source }}</span>
                </div>
              </div>
            </fieldset>
            <fieldset class="b-form-group form-group">
              <div class="form-row">
                <legend class="col-form-legend col-sm-1">
                  <i class="inc_event_black mr-1"></i>
                </legend>
                <div class="col-sm-11">
                  <span class="form-control_edit">{{ dateTime }}</span>
                </div>
              </div>
            </fieldset>
            <fieldset class="b-form-group form-group">
              <div class="form-row">
                <legend class="col-form-legend col-sm-1">
                  <i class="inc_subject_black mr-1"></i>
                </legend>
                <div class="col-sm-11">
                  <span class="form-control_edit">{{ description }}</span>
                </div>
              </div>
            </fieldset>
          </div>
        </b-form>
      </div>
    </div>

    <div class="cand-card__body-info_part-2">
      <fieldset class="b-form-group form-group">
        <div class="form-row">
          <legend class="col-form-legend pb-3">
            <span class="cand-card__body-info__main-title"> </span>
          </legend>
          <div class="col-sm-12 multi-tag">
            <multiselect
              v-model="valueTag"
              tag-placeholder="   "
              placeholder="   "
              label="name"
              track-by="code"
              :options="optionsTag"
              :multiple="true"
              :taggable="true"
              @tag="addTag"
            ></multiselect>
          </div>
        </div>
      </fieldset>
    </div>

    <div class="cand-card__body-info_part-3">
      <fieldset class="b-form-group form-group absolute-part">
        <div class="form-row">
          <legend class="col-form-legend pb-3">
            <span class="cand-card__body-info__main-title"> </span>
          </legend>
          <div class="col-sm-12 multi-tag">
            <b-form-fieldset>
              <div class="form-row">
                <div class="col-sm-4 text-truncate">
                  <b-form-input
                    type="text"
                    placeholder="  "
                    v-model="addField.label"
                    maxlength="200"
                    class="mt-0 pt-0"
                  ></b-form-input>
                </div>
                <div class="col-sm-8 text-truncate">
                  <div class="position-relative">
                    <b-form-input
                      type="text"
                      placeholder=" "
                      v-model="addField.item"
                      maxlength="200"
                      class="mt-0 pt-0"
                    ></b-form-input>
                    <i
                      class="ic_add action-tag-other-field position-absolute"
                      @click="addInput('addField')"
                    ></i>
                  </div>
                </div>
              </div>
            </b-form-fieldset>
          </div>
        </div>
        <div class="cand-card__body-info_new-field-wrapper">
          <div v-for="(input, index) in addedFields" :key="index">
            <div class="row added-filed-wrapper">
              <div class="col-sm-4 flex pl-0 text-truncate">
                <i
                  class="
                    ic_close
                    action-tag-other-field action-tag-other-field_remove
                  "
                  @click="deleteInput('deleteField', index)"
                ></i>
                <span>{{ input.label }}</span>
              </div>
              <div class="col-sm-8 text-truncate">
                <span>{{ input.item }}</span>
              </div>
            </div>
          </div>
        </div>
      </fieldset>
    </div>
  </div>
</template>

<style lang="scss">
.added-filed-wrapper {
  position: relative;
  .action-tag-other-field_remove {
    display: none;
    position: absolute;
    top: 1px;
    right: 15px;
  }
  &:hover {
    .action-tag-other-field_remove {
      display: flex;
    }
  }
}
.action-tag-other-field {
  color: #666;
  background: #e1e1e1;
  height: 20px;
  width: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  top: 12px;
  border-right: 10px solid #fff;
  z-index: 1;
  left: 0;
  font-size: 13px;
  &:before {
    font-size: 9px;
  }
  &_remove {
    border-right: 0;
    width: 18px;
    height: 18px;
    float: right;
    margin-left: 9px;
  }
}
.multiselect {
  .multiselect__select {
    color: #666;
    background: #e1e1e1;
    height: 20px;
    width: 30px;
    top: 6px;
    border-right: 10px solid #fff;
    z-index: 1;
    left: 0;
    &:before {
      font-size: 9px;
    }
  }
  .multiselect__input {
    position: absolute;
    top: 0;
    width: 100% !important;
    right: 0;
    padding-bottom: 5px !important;
    border-bottom: 1px solid #e1e1e1;
  }
  .multiselect__placeholder {
    display: none;
  }

  .multiselect__tags-wrap {
    display: inline-block;
    height: 110px;
    margin-top: 35px;
    overflow-y: auto;
    @media (max-width: 1440px) {
      height: 70px;
    }
  }
  .multiselect__tags {
    border: 0;
    padding-left: 0px !important;
    padding-top: 0;
  }
  .multiselect__tag {
    padding: 6px 26px 6px 9px;
    color: #393e7a;
    .multiselect__tag-icon {
      top: 3px;
    }
    &:nth-child(1n) {
      background: #f1dce8;
    }
    &:nth-child(2n) {
      background: #ffd391;
    }
    &:nth-child(3n) {
      background: #8fd2ff;
    }
    &:nth-child(4n) {
      background: #c2ffbb;
    }
    &:nth-child(5n) {
      background: #e99797;
    }
    &:nth-child(6n) {
      background: #e1e1e1;
    }
  }
}

.cand-card .cand-card__edit-field {
  margin: 0 auto 1rem;
  > .form-group {
    border-bottom: 0;
    padding: 0 5px;
  }
  .field-group {
    border: 1px solid #e1e1e1;
    padding: 5px 10px;
  }
  .form-group {
    margin-bottom: 0;
  }
}
.absolute-part {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: flex;
  flex-direction: column;
}
.cand-card__body-info {
  height: 100%;
  overflow: hidden;
  .cand-card__body-info_part-1 {
    height: 50%;
    overflow: hidden;
    margin-bottom: 1rem;
    position: relative;
    > div {
      position: relative;
      height: 100%;
    }
  }
  .cand-card__body-info_part-2 {
    height: 27%;
    margin-bottom: 1rem;
  }
  .cand-card__body-info_part-3 {
    height: 30%;
    overflow: hidden;
    position: relative;
  }
  i {
    font-size: 16px;
  }
  .col-form-legend {
    display: flex;
    align-items: center;
    padding-bottom: 0 !important;
  }
  &__main-title {
    font-size: 13px;
    font-weight: bold;
  }
  .cand-card__body-info_new-field-wrapper {
    overflow-x: hidden;
    overflow-y: auto;
  }
}
</style>

<script>
import Multiselect from "vue-multiselect";

export default {
  name: "CandidateUserDetails",
  components: {
    // for selecting and taging
    Multiselect,
  },
  data() {
    return {
      image: "",
      email: "m.rastegar991@gmail.com",
      phone: "09358537536",
      social: "linkedin.com/m.rastegar",
      link: "test1,test2,test3",
      source: "source test",
      dateTime: "1400/020/07",
      description:
        "        .         ",
      isActive: false,
      valueTag: [
        { name: "Vue.js", language: "JavaScript" },
        { name: "Adonis", language: "JavaScript" },
        { name: "Rails", language: "Ruby" },
        { name: "Sinatra", language: "Ruby" },
        { name: "Laravel", language: "PHP" },
        { name: "Phoenix", language: "Elixir" },
      ],
      optionsTag: [
        { name: "Vue.js", code: "vu" },
        { name: "Javascript", code: "js" },
        { name: "Open Source", code: "os" },
      ],
      addField: {
        item: "",
        label: "",
      },
      addedFields: [
        { item: "item", label: "label" },
        { item: "item", label: "label" },
        { item: "item", label: "label" },
        { item: "item", label: "label" },
        { item: "item", label: "label" },
      ],
    };
  },
  methods: {
    validateBeforeSubmit() {
      this.$validator.validateAll().then((result) => {
        if (result) {
          // eslint-disable-next-line
          alert("From Submitted!");
          return;
        }

        alert("Correct them errors!");
      });
    },
    // for selecting and taging
    addTag(newTag) {
      const tag = {
        name: newTag,
        code: newTag.substring(0, 2) + Math.floor(Math.random() * 10000000),
      };
      this.optionsTag.push(tag);
      this.valueTag.push(tag);
    },
    editTalent() {
      this.isActive = !this.isActive;
    },

    // ----- add row input
    addInput(inputScope) {
      if (inputScope == "addField") {
        this.addedFields.unshift({
          item: this.addField.item,
          label: this.addField.label,
        });

        this.addField = {
          item: "",
          label: "",
        };
      }
    },

    // remove row input
    deleteInput(inputScope, index) {
      if (inputScope == "deleteField") {
        this.addedFields.splice(index, 1);
      }
    },
  },
};
</script>


