<template>
    <div>
      <b-nav-item-dropdown right no-caret>
        <template slot="button-content">
          <i class="ic_add"></i>
		      <i class="ic_DownArrow ml-2"></i>
        </template>
        <b-dropdown-header tag="div" class="text-center"><strong></strong></b-dropdown-header>
        <b-dropdown-item v-b-modal.modal1><i class="ic_user"></i> </b-dropdown-item>
        <b-dropdown-item v-b-modal.modal2><i class="ic_upload"></i>   </b-dropdown-item>
        <b-dropdown-item><i class="ic_jobs"></i> </b-dropdown-item>
        <b-dropdown-item to="talentpool"><i class="ic_TalentPool"></i> </b-dropdown-item>
        <b-dropdown-item><i class="ic_team"></i> </b-dropdown-item>
      </b-nav-item-dropdown>

        <!-- Modal Component -->
        <b-modal hide-footer id="modal1" class="text-left" title="  ">
          	<FormApplicant/>
        </b-modal>

        <b-modal hide-footer ref="uploadModal" id="modal2" class="text-left upload__modal" title="  ">
          <b-card no-body class="candidate__tab-list">
            <b-tabs pills card vertical nav-wrapper-class="candidate__accor-item">
              <div class="candidate__group-upload">
                <button class="btn btn-primary btn-sm"> </button>
              </div>
              <div class="candidate__close">
                <button class="btn btn-primary btn-sm" @click="hideModal"></button>
              </div>
              <b-tab title='<span class="option__condid-img"><img src="static/img/avatars/66.jpg" class="option__image rounded-circle" style="width: 35px; height: 35px;"></span><span class="option__title"></span> <span class="option__small"></span>'
              active>
                <div class="m-0">
                  <div class="candidate__pad-item candidate__pad-candform candidate__auto-height">
                    <FormApplicantBig/>
                  </div>
                </div>
              </b-tab>
            </b-tabs>
          </b-card>
        </b-modal>

    </div>
</template>
<script>
	import FormApplicant from "../views/candidates/FormApplicant.vue"
	import FormApplicantBig from "../views/candidates/FormApplicantBig.vue"

  export default {
    name: 'header-addnew',
      components: {
        FormApplicantBig,
        FormApplicant
      },
    data: () => {
      return {
        image: "",
				emails: [],
				phones: [],
      }
    },
    methods:{
          hideModal () {
      this.$refs.uploadModal.hide()
    }
    }
  }
</script>




