
<template>
  <c-switch
    type="text"
    variant="primary-outline-alt"
    :on="on"
    :off="off"
    :pill="true"
    :checked="true"
    :v-model="status"
  />
</template>
<style lang="scss">
.switch-tab {
  .switch.switch-text {
    margin-bottom: 0;
    width: 63px;
    height: 24px;
    .switch-label {
      border-radius: 12px !important;
      border: solid 1px #e1e1e1;
      background-color: #ffffff;
      overflow: hidden;
      font-size: 12px;
      font-weight: normal;
      display: flex;
      align-items: center;
      justify-content: space-around;
      &::after {
        opacity: 1;
        color: #393e7a;
        z-index: 2;
        position: static;
        margin-top: 0;
        padding: 0;
      }
      &::before {
        opacity: 1;
        color: #bebebe;
        z-index: 2;
        position: static;
        margin-top: 0;
        padding: 0;
      }
    }
    .switch-input:checked ~ .switch-label {
      &::after {
        opacity: 1;
        z-index: 2;
        color: #bebebe;
      }
      &::before {
        opacity: 1;
        z-index: 2;
        color: #393e7a;
      }
    }
    .switch-handle {
      z-index: 1;
      background: #e1e1e1 !important;
      width: 50%;
      text-align: center;
      overflow: hidden;
      box-shadow: none;
      border-radius: 12px !important;
      height: 24px;
      top: 0;
      left: 0;
      border: 0;
    }
  }
  .switch-primary-outline-alt > .switch-input:checked ~ .switch-handle {
    background-color: #e1e1e1;
    width: 54%;
    left: 46%;
    top: 0;
  }
}
</style>
<script>
import cSwitch from "../../../components/Switch";

export default {
  name: "CustomSwitch",
  components: {
    cSwitch,
  },
  props: ["on", "off", "status"],

  data() {},
  methods: {},
};
</script>


