<template>
  <div class="clearfix cand-card cand-card__inner-modal">
    <div class="cand-card__main profile-form">
      <div class="cand-card__top cand-card__header d-flex align-items-center">
        <div class="profile-avatar img-avatar mr-2">
          <div v-if="!image" class="img-avatar_less" @click="trigger">
            <i class="profile-avatar__icon ic_image"></i>
            <input type="file" @change="onFileChange" ref="onFileChange" />
          </div>

          <div v-else>
            <div class="img-avatar_size img-avatar">
              <img :src="image" />
            </div>
            <i @click="removeImage" class="ic_edit img-avatar_remove"></i>
          </div>
        </div>
        <span class="cand-card__star-rate cand-card_big-star">
          <i class="ic_StarFull"></i>
          <i class="ic_StarFull"></i>
          <i class="ic_StarFull"></i>
          <i class="ic_star"></i>
          <i class="ic_star"></i>
        </span>
      </div>
    </div>
    <div class="cand-card__mid">
      <CardStatusBar />
    </div>
    <div class="cand-card__bottom">
      <div class="cand-card__side-right">
        <div
          id="right_panel"
          class="cand-card__side-detail cand-card__side-part"
        >
          <DetailsSide />
        </div>
        <div class="cand-card__side-review cand-card__side-part">
          <ReviewSide />
        </div>
      </div>
      <div class="cand-card__side-left">
        <div class="collapse-side-arrow" @click="collapseSide">
          <i class="inc_keyboard_backspace_black" />
        </div>
        <CardBodyInfo />
      </div>
    </div>
  </div>
</template>

<script>
import DetailsSide from "./partials/detailsSide";
import ReviewSide from "./partials/reviewSide";
import UserDetails from "./partials/userDetails";
import CardBodyInfo from "./partials/bodyInfo";
import CardStatusBar from "./partials/statusBar";

export default {
  name: "candform-candcard",
  components: {
    // for selecting and taging
    DetailsSide,
    ReviewSide,
    UserDetails,
    CardBodyInfo,
    CardStatusBar,
  },
  data() {
    return {};
  },
  methods: {
    layoutResize(panel) {
      const BORDER_SIZE = 4;
      let m_pos;
      function resize(e) {
        const dx = m_pos - e.x;
        m_pos = e.x;
        panel.style.width =
          parseInt(getComputedStyle(panel, "").width) + dx + "px";
      }

      panel.addEventListener(
        "mousedown",
        function (e) {
          if (e.offsetX < BORDER_SIZE) {
            m_pos = e.x;
            document.addEventListener("mousemove", resize, false);
          }
        },
        false
      );

      document.addEventListener(
        "mouseup",
        function () {
          document.removeEventListener("mousemove", resize, false);
        },
        false
      );
    },

    // ---- profile image and email , phone adder
    trigger() {
      this.$refs.onFileChange.click();
    },
    onFileChange(e) {
      var files = e.target.files || e.dataTransfer.files;
      if (!files.length) {
        return;
      }
      this.createImage(files[0]);
    },
    createImage(file) {
      var reader = new FileReader();
      var vm = this;

      reader.onload = (e) => {
        vm.image = e.target.result;
      };
      reader.readAsDataURL(file);
    },
    removeImage: function (e) {
      this.image = "";
    },
    collapseSide() {
      document
        .querySelector(".cand-card__side-left")
        .classList.toggle("collapse-side");
    },
  },
  mounted() {
    const panel = document.getElementById("right_panel");
    this.layoutResize(panel);
  },
};
</script>

<style lang="scss">
.cand-card {
  background: white;
  .mw-350 {
    min-width: 350px;
  }
  .overflow-x-auto {
    overflow-x: auto;
  }
}
.cand-card__bottom {
  position: relative;
  width: 100%;
  background: white;
  display: flex;
  flex-wrap: nowrap;
  overflow: hidden;
  flex: 1;
}
.cand-card__side-left {
  background: #f9fafa;
  padding: 15px;
  position: relative;
  min-width: 320px !important;
  max-width: 320px !important;
  .collapse-side-arrow {
    position: absolute;
    font-size: 24px;
    top: 14px;
    left: 15px;
    z-index: 3;
  }
  &.collapse-side {
    overflow: hidden;
    min-width: auto !important;
    width: 0;
    .collapse-side-arrow {
      right: 2px;
      left: auto;
      transform: rotate(180deg);
      top: 2px;
    }
    &:before {
      content: "";
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      background: #f9fafa;
      z-index: 2;
    }
    &:after {
      position: absolute;
      content: "";
      top: 55px;
      right: -10px;
      width: 50px;
      z-index: 10;
      transform: rotate(-90deg);
      color: #7b7b7b;
    }
    .collapse-side-arrow {
      right: 0;
      left: auto;
    }
  }
  .cand-card__user-detail {
    width: 100%;
  }
}
.cand-card__side-right {
  position: relative;
  width: 100%;
  display: flex;
  flex-wrap: nowrap;
  border-left: 1px solid #e1e1e1;
  max-width: calc(100% - 30px);
  .cand-card__side-part {
    flex: 1;
    min-width: 450px;
    display: flex;
    flex-direction: column;
    > .cand-card__main {
      flex: 1;
      display: flex;
      flex-direction: column;
    }
  }
  #right_panel {
    border-left: 1px solid #e1e1e1;
    position: relative;
    padding-left: 4px;
    min-width: 50%;
    flex: initial;
    &:after {
      content: "";
      // background-color: #e1e1e1;
      position: absolute;
      left: 0;
      top: 0;
      width: 4px;
      height: 100%;
      cursor: w-resize;
    }
  }
  .cand-card__tab-page {
    position: absolute;
    left: 0.5rem;
    right: 0.5rem;
    top: 0.5rem;
    bottom: 0.5rem;
    display: flex;
    /* overflow: hidden; */
    flex-direction: column;
  }
}
.overflow-y-auto {
  overflow-y: auto;
}
.modal-cand__full {
  .modal-dialog {
    width: calc(100vw - 100px);
    max-width: 1580px !important;
    display: flex;
    flex-direction: column;
    height: 800px;
    @media (max-width: 1440px) {
      height: 610px;
    }
  }
  .modal-content {
    flex: 1;
  }
  .cand-card.cand-card__inner-modal {
    display: flex;
    flex-direction: column;
    flex: 1;
    height: 100%;
  }
  .profile-form .profile-avatar {
    height: 46px;
    width: 46px;
  }
}
</style>


