<template>
  <div class="cand-card__interview cand-card__tab-page">
    <!-- <div class="cand-card__expert mb-2">
      <strong> </strong>
    </div> -->
    <b-form @submit.prevent="validateBeforeSubmit" class="mb-3">
      <div class="field-group bg-white">
        <div class="row">
          <b-form-fieldset class="col-sm-5">
            <SelectDropDown
              fill="true"
              :defaultOption="dropdown_1"
              :options="options"
            />
          </b-form-fieldset>

          <b-form-fieldset class="col-sm-7 d-flex align-items-center">
            <c-switch
              type="icon"
              variant="primary-outline-alt"
              class="custom-circle-select"
              :pill="true"
              :checked="false"
              v-model="status"
            />

            <span
              class="pr-2"
              :style="{ color: status ? '#393e7a' : '#a8a4a4' }"
              >   </span
            >
          </b-form-fieldset>
          <b-form-fieldset class="col-sm-5">
            <div class="text-truncate multi-tag fix-margin">
              <div class="position-relative d-flex align-items-center">
                <i class="inc_people_black font-24 mr-1" />
                <b-form-input
                  type="text"
                  placeholder=" "
                  maxlength="200"
                  readonly
                  :value="selected_interviewer"
                ></b-form-input>
                <i
                  class="ic_add action-tag-other-field position-absolute mt-1"
                ></i>
              </div>
            </div>
          </b-form-fieldset>
          <div class="row col-sm-7 pl-0" v-if="!status">
            <b-form-fieldset class="col-sm-7">
              <div class="text-truncate multi-tag fix-margin">
                <div class="position-relative d-flex align-items-center">
                  <i class="inc_event_black font-24 mr-1" />
                  <b-form-input
                    type="text"
                    placeholder=" "
                    maxlength="200"
                    readonly
                    :value="selected_interviewer"
                  ></b-form-input>
                  <i
                    class="ic_add action-tag-other-field position-absolute mt-1"
                  ></i>
                </div>
              </div>
            </b-form-fieldset>
            <b-form-fieldset class="col-sm-5 pl-0 pr-0">
              <div class="text-truncate multi-tag fix-margin">
                <div class="position-relative d-flex align-items-center">
                  <i class="inc_schedule_black font-24 mr-1" />
                  <b-form-input
                    type="text"
                    placeholder=" "
                    maxlength="200"
                    readonly
                    :value="selected_interviewer"
                  ></b-form-input>
                  <i
                    class="ic_add action-tag-other-field position-absolute mt-1"
                  ></i>
                </div>
              </div>
            </b-form-fieldset>
          </div>
          <div class="row col-sm-7 pl-0" v-if="status">
            <div class="w-100 pr-3">
              <SelectDropDown :defaultOption="dropdown_1" :options="options" />
            </div>
          </div>
          <b-form-fieldset class="col-sm-12">
            <div class="text-truncate multi-tag fix-margin">
              <div class="position-relative d-flex align-items-center">
                <i class="inc_pin_drop_black font-24 mr-1" />
                <b-form-input
                  type="text"
                  placeholder=" "
                  maxlength="200"
                  readonly
                  :value="selected_interviewer"
                ></b-form-input>
                <i
                  class="
                    inc_add_location_black
                    action-tag-other-field
                    position-absolute
                    mt-1
                  "
                ></i>
              </div>
            </div>
          </b-form-fieldset>

          <b-form-fieldset class="col-sm-12">
            <legend class="col-form-legend">
              <span></span>
            </legend>
            <b-form-textarea
              type="text"
              v-model="feedbackDescription"
              :rows="3"
              :max-row="6"
              placeholder=" "
              maxlength="1000"
            ></b-form-textarea>
          </b-form-fieldset>

          <b-form-fieldset class="col-sm-12">
            <div class="d-flex align-items-center mt-1">
              <div class="mr-auto d-flex align-items-center">
                <i class="inc_attach_file_black font-24" />
                <button type="submit" class="btn ml-2 btn-primary btn-sm">
                  
                </button>
              </div>
            </div>
          </b-form-fieldset>
        </div>
      </div>
    </b-form>
    <b-form-checkbox-group
      id="checkbox-group-1"
      v-model="selected"
      name="flavour-1"
      class="cand-card__rate-board"
    >
      <ul>
        <li v-for="(input, index) in jobsGroup" :key="index">
          <div class="cand-card__rate-board-header d-flex align-items-center">
            <span class="option__image rounded-circle rounded-circle_smallsize">
              <img
                src="static/img/avatars/66.jpg"
                class="option__image rounded-circle"
                style="width: 35px; height: 35px"
              />
            </span>
            <p class="mb-0 pr-2 pl-4">{{ input.title }}</p>
            <span class="mr-auto col-auto mr-auto col-auto pl-0">
              <span class="d-flex">
                <span class="mr-2">
                  <i
                    class="inc_info_black"
                    v-show="!isActive"
                    @click="editTalent"
                  />
                </span>
                <span class="mr-2">
                  <i
                    class="inc_delete_forever_black"
                    v-show="!isActive"
                    @click="editTalent"
                  />
                </span>
                <span class="">
                  <i
                    class="inc_close_black"
                    v-show="isActive"
                    @click="editTalent"
                  />
                  <i
                    class="ic_tick mr-2"
                    v-show="isActive"
                    @click="editTalent"
                  />
                  <i
                    class="inc_edit_black"
                    v-show="!isActive"
                    @click="editTalent"
                  />
                </span>
              </span>
            </span>
          </div>
          <div class="user-review-body">
            <span class="pr-2 d-flex align-items-center">
              <i class="inc_people_black font-24 mr-1" />
              <span>
                {{ input.description }}
              </span>
              <i class="inc_restart_alt_black mr-auto" />
            </span>
          </div>
        </li>
      </ul>
    </b-form-checkbox-group>
  </div>
</template>
<style lang="scss">
.fix-margin {
  position: relative;
  top: -2px;
  input {
    width: calc(100% - 40px);
  }
}
.cand-card {
  .cand-card__rate-board li {
    padding: 0;
    > * {
      padding: 10px;
    }
    .user-review-body {
      border-top: 1px solid #e1e1e1;
    }
  }
  .cand-card__jobs {
    input[type="checkbox"]:checked + span + span {
      text-decoration: line-through;
    }
    .custom-control.custom-checkbox {
      display: flex;
      align-items: center;
    }
  }
}
</style>
<script>
import cSwitch from "../../../components/Switch";
import SelectDropDown from "../SelectDropDown";
import Multiselect from "vue-multiselect";

export default {
  name: "Jobs",
  components: {
    cSwitch,
    Multiselect,
    SelectDropDown,
  },
  data() {
    return {
      image: "",
      email: "m.rastegar991@gmail.com",
      phone: "09358537536",
      social: "linkedin.com/m.rastegar",
      link: "test1,test2,test3",
      source: "source test",
      isActive: false,
      valueTag: [{ name: "Javascript", code: "js" }],
      selected_interviewer: " , ",
      jobsGroup: [
        {
          title: " ",
          description: " , ",
        },
        {
          title: " ",
          description: " , ",
        },
        {
          title: " ",
          description: " , ",
        },
        {
          title: " ",
          description: " , ",
        },
        {
          title: " ",
          description: " , ",
        },
        {
          title: " ",
          description: " , ",
        },
      ],
      isActive: false,
      status: false,
      dropdown_1: { title: " ", active: false },
      dropdown_2: { title: " ", active: false },
      options: [
        { title: " 1 ", active: true },
        { title: " 12 ", active: true },
        { title: " 123 ", active: true },
      ],
    };
  },

  methods: {
    setDropDown1(value) {
      this.dropdown_1 = value;
    },
    setDropDown2(value) {
      this.dropdown_2 = value;
    },
    editTalent() {
      this.isActive = !this.isActive;
    },
  },
};
</script>


