
<template>
  <div class="cand-card__questionnaire cand-card__tab-page">
    <div class="row mb-4">
      <div class="col w-100">
        <SelectDropDown :defaultOption="defaultOption" :options="options" />
      </div>
      <div class="col-auto">
        <div class="clearfix mt-1">
          <button type="submit" class="btn float-right ml-2 btn-primary btn-sm">
            
          </button>
        </div>
      </div>
    </div>
    <ul class="cand-card__rate-board">
      <li
        class="user-review-header"
        v-for="(input, index) in reviewGroup"
        :key="index"
      >
        <div class="d-flex align-items-center pt-2 pb-2">
          <!-- <i class="ic_edit" /> -->
          <p class="mb-0 pr-2 pl-4">{{ input.label }}</p>
          <span class="mr-auto col-auto pl-0 mt-1">
            <span class="">
              <i :id="`tooltip-ques-${index}`" class="inc_info_black" />
              <i class="inc_restart_alt_black mr-1" />
              <i class="inc_preview_black mr-1" />
              <Tooltip
                :target="`tooltip-ques-${index}`"
                :data="input.tooltip"
              />
            </span>
          </span>
        </div>
      </li>
    </ul>
  </div>
</template>
<style lang="scss">
</style>
<script>
import SelectDropDown from "../SelectDropDown";
import Tooltip from "../Tooltip";

export default {
  name: "CandidateCardQuestionnaire",
  components: {
    SelectDropDown,
    Tooltip,
  },
  data() {
    return {
      reviewGroup: [
        {
          label: "     ",
          tooltip: [
            {
              title: "title",
              description: "description",
            },
            {
              title: "title",
              description: "description",
            },
          ],
        },
        {
          label: "     ",
          tooltip: [
            {
              title: "title",
              description: "description",
            },
            {
              title: "title",
              description: "description",
            },
          ],
        },
        {
          label: "     ",
          tooltip: [
            {
              title: "title",
              description: "description",
            },
            {
              title: "title",
              description: "description",
            },
          ],
        },
      ],
      defaultOption: { title: " ", active: false },
      options: [
        { title: " 1 ", active: true },
        { title: " 12 ", active: true },
        { title: " 123 ", active: true },
      ],
    };
  },
  methods: {
    setDropDown1(value) {
      this.dropdown_1 = value;
    },
  },
};
</script>


