<template>
  <div class="timeline">
    <div
      class="time-line-item"
      :class="{
        left: (index + 1) % 2 === 0,
        right: (index + 1) % 2 !== 0,
      }"
      v-for="(input, index) in reviewGroup"
      :key="index"
    >
      <div class="content">
        <div class="cand-card__rate-board-header d-flex align-items-center">
          <span class="option__image rounded-circle rounded-circle_smallsize"
            ><img
              src="static/img/avatars/66.jpg"
              class="option__image rounded-circle"
              style="width: 35px; height: 35px"
          /></span>
          <p class="mb-0 pr-2 pl-4 avatar-name"> </p>
        </div>
        <div class="mt-1 content-description">   </div>
      </div>
      <div class="mt-1 content-date">
        <span class="option__title">10/02/2018</span>
        <span class="option__title">16:05</span>
      </div>
    </div>
  </div>
</template>

<style lang="scss" scoped>
/* The actual timeline (the vertical ruler) */
.timeline {
  position: relative;
  max-width: 600px;
  margin: 0 auto;
  direction: ltr;
}

/* The actual timeline (the vertical ruler) */
.timeline::after {
  content: "";
  position: absolute;
  width: 3px;
  background-color: #393e7a;
  top: 0;
  bottom: 0;
  left: 50%;
  margin-left: -2px;
}

/* time-line-item around content */
.time-line-item {
  padding: 10px 18px 10px 0;
  position: relative;
  background-color: inherit;
  width: 50%;
  .content-date {
    position: absolute;
    top: 18px;
  }
  /* Place the time-line-item to the left */
  &.left {
    left: 0;
    &::before {
      content: " ";
      position: absolute;
      top: 25px;
      z-index: 1;
      right: 13px;
      width: 12px;
      height: 12px;
      border: 1px solid #bebebe;
      background: #f6f6f6;
      transform: rotate(45deg);
      border-bottom-color: transparent;
      border-left-color: transparent;
    }
    .content-date {
      right: -55%;
    }
  }

  /* Place the time-line-item to the right */
  &.right {
    left: 50%;
    padding: 10px 0 10px 18px;
    &::before {
      content: " ";
      position: absolute;
      top: 25px;
      z-index: 1;
      left: 13px;
      width: 12px;
      height: 12px;
      border: 1px solid #bebebe;
      background: #f6f6f6;
      transform: rotate(45deg);
      border-top-color: transparent;
      border-right-color: transparent;
    }
    &::after {
      left: -5px;
    }
    .content-date {
      left: -55%;
    }
  }
}

/* The circles on the timeline */
.time-line-item::after {
  content: "";
  position: absolute;
  width: 11px;
  height: 11px;
  right: -5px;
  background-color: #ffb300;
  top: 26px;
  border-radius: 50%;
  z-index: 1;
}

/* The actual content */
.content {
  padding: 8px;
  background-color: #f6f6f6;
  position: relative;
  direction: rtl;
  border: 1px solid #bebebe;
  font-size: 13px;
  .content-description {
    color: #7b7b7b;
    font-weight: 300;
  }
}
</style>

<script>
export default {
  name: "TimeLine",
  data() {
    return {
      reviewGroup: [1, 2, 3, 4, 5],
    };
  },
};
</script>


