<template>
  <div class="canc-card__furtherInfo cand-card__tab-page">
    <ul class="cand-card__rate-board">
      <li
        class="user-review-header"
        v-for="(input, index) in reviewGroup"
        :key="index"
      >
        <div class="d-flex align-items-center pt-2 pb-2">
          <i class="inc_topic_black mt-1" />
          <p class="mb-0 pr-2 pl-4 w-50">  </p>
          <p class="mb-0 pr-2 pl-4 w-50">
            <b-form-textarea
              v-show="input.isActive"
              :name="input.id"
              :rows="3"
              :max-row="6"
              maxlength="1000"
              v-model="input.title"
              placeholder="    "
            ></b-form-textarea>

            <span class="form-control_edit" v-show="!input.isActive">{{
              input.title
            }}</span>
          </p>
          <span class="mr-auto mt-1">
            <i
              class="inc_edit_black"
              v-show="!input.isActive"
              @click="
                editTalent(index, {
                  id: input.id,
                  title: input.title,
                  isActive: true,
                })
              "
            />
            <i
              class="inc_done_black"
              v-show="input.isActive"
              @click="
                editTalent(index, {
                  id: input.id,
                  title: input.title,
                  isActive: false,
                })
              "
            />
          </span>
        </div>
      </li>
    </ul>
  </div>
</template>
<style lang="scss">
.canc-card__furtherInfo {
  textarea.form-control {
    max-height: initial;
  }
}
</style>
<script>
export default {
  name: "CandidateCardActivity",
  data() {
    return {
      reviewGroup: [
        {
          title: " 1",
          id: "qa-1",
          isActive: false,
        },
        {
          title: " 2",
          id: "qa-2",
          isActive: false,
        },
      ],
    };
  },
  methods: {
    editTalent(index, input) {
      this.$set(this.reviewGroup, index, input);
    },
  },
};
</script>


