<template>
	<div class="talentpool">
		<div class="b-form-group form-group">
			<b-button type="button" size="sm" variant="primary" @click="addForm"><i class="ic_add mr-2"></i>  </b-button>
		</div>
		<b-card class="talentpool_border-dashed" v-if="isActive">
			<div>
				<b-form @submit.prevent="validateBeforeSubmitNewTalent">
					<div class="row form-group">
						<div class="col-sm-6">							
							<b-input id="listTitle" type="text" placeholder=" " maxlength="50" name="listTitle" v-model="listTitle" v-validate="'required|alpha'" :class="{'input': true, 'border-danger': errors.has('listTitle') }"></b-input>
						</div>
						<div class="col-sm-6">
							<b-input id="talentinlineInput2" type="text" placeholder="" maxlength="500"></b-input>
						</div>
					</div>

					<div class="form-row">
						<div class="col-sm-9">
							<legend class="col-form-legend cfl-padding">
								<strong>{{optionTotalVar}} &nbsp;</strong>
								<span></span>
							</legend>
							<div class="condid-checker">

								<div style="display:inline-block" v-for="checkedName in checkedNames">
									<div class="option__condid-img mb-0" v-if="checkedName.img">
										<img style="width:45px" class="option__image rounded-circle" :src="checkedName.img">
									</div>
									<div class="option__condid-img option__condid-img_text mb-0">
										<div class="option__image rounded-circle">
											<span>
												{{checkedName.fname.substring(0,1)}}
											</span>
											<span>
												{{checkedName.lname.substring(0,1)}}
											</span>
										</div>
									</div>
								</div>
								<div class="option__condid-img option__condid-img_text oc-plus mb-0" v-b-modal.talentpool-new-modal>
									<div class="option__image rounded-circle">
										<span class="ic_add"></span>
									</div>
								</div>

								<b-modal hide-footer v-model="showModal" id="talentpool-new-modal" class="text-left modal-gray" title=" ">
									<div>
										<b-container>
											<ul class="ul-checkbox">
												<li v-for="item in condidate" class="table-box"> 
													<!-- <input type="checkbox"   checked> -->
													<div class="table-box__row">
														<b-form-checkbox class="ul-checkbox__input table-box__cell" v-model="checkedNames" :value="condidate[item.id]">
															<div class="table-box__cell oci">
																<div class="option__condid-img" v-if="item.img">
																	<img style="width:60px;height:60px;" class="option__image rounded-circle" :src="item.img">
																</div>
																<div class="option__condid-img option__condid-img_text">
																	<div class="option__image rounded-circle rounded-circle_bigsize">
																		<span>{{item.fname.substring(0,1)}}</span>
																		<span>{{item.lname.substring(0,1)}}</span>
																	</div>
																</div>
															</div>
															<div class="option__desc table-box__cell">
																<div class="row">
																	<span class="col">
																		<span class="option__title">{{ item.fname }}</span>
																		<span class="option__small">{{ item.lname }}</span>
																	</span>
																	<span class="option__small col">{{ item.description }}</span>
																</div>
															</div>

														</b-form-checkbox>

													</div>
												</li>
											</ul>
										</b-container>
										<div slot="modal-footer" class="w-100 modal-footer">
											<b-btn size="sm" class="float-right ml-2" variant="success" @click="showModal=false">
											 
											</b-btn>
											<b-btn size="sm" class="float-right" variant="default" @click="showModal=false">
											
											</b-btn>
										</div>
									</div>
								</b-modal>
								
							</div>
						</div>
						<div class="col-sm-3 talentpool_height">
							<div class="clearfix talentpool_position-abs">
								<b-button class="float-right" type="submit" size="sm" variant="primary"></b-button>
								<b-button class="float-right mr-2 btn btn-link" @click="removeForm" type="reset" size="sm"></b-button>
							</div>
						</div>
					</div>


				</b-form>
			</div>
		</b-card>

		<!-- <special-item-add :item="item"></special-item-add> -->

		<b-card v-for="item in specialList">
			<special-item :item="item"></special-item>
		</b-card>

	</div>





</template>

<script>
	// for selecting and taging
	import Multiselect from "vue-multiselect";
	import specialItem from "./specialItem";

	export default {
		name: "form-talentpool",
		components: {
			// for selecting and taging
			Multiselect,
			specialItem
		},
		data() {
			return {
				showModal:false,
				specialList: [
					{
						listTitle: ' ',
						listDescription: '  ',
						condidate: [
							{
								id:0,
								fname: "",
								lname: "", img: "",
								description: " ",
								selected: true

							},
							{
								id:1,
								fname: "",
								lname: "",
								img: "static/img/avatars/2.jpg",
								description: "",
								selected: true

							},
							{	
								id:2,
								fname: "",
								lname: "",
								img: "static/img/avatars/66.jpg",
								description: " ",
								selected: true
							},
							{
								id:3,
								fname: "",
								lname: "",
								img: "",
								description: " ",
								selected: true
							},
						],
						checkedNames: [
							{
								id:0,
								fname: "",
								lname: "", img: "",
								description: " ",
								selected: true

							},
							{
								id:1,
								fname: "",
								lname: "",
								img: "static/img/avatars/2.jpg",
								description: "",
								selected: true

							},
						],
						value: [],
						optionTotalVar: "",
					},

				],

				condidate: [
					{
						id:0,
						fname: "",
						lname: "", img: "",
						description: " ",
						selected: true

					},
					{
						id:1,
						fname: "",
						lname: "",
						img: "static/img/avatars/2.jpg",
						description: "",
						selected: true

					},
					{	
						id:2,
						fname: "",
						lname: "",
						img: "static/img/avatars/66.jpg",
						description: " ",
						selected: true
					},
					{
						id:3,
						fname: "",
						lname: "",
						img: "",
						description: " ",
						selected: true
					},
				],
				value: [],
				optionTotalVar: "",
				isActive: false,
				listTitle:'',
				checkedNames: [
					{
						id:0,
						fname: "",
						lname: "", img: "",
						description: " ",
						selected: true

					},
					{
						id:1,
						fname: "",
						lname: "",
						img: "static/img/avatars/2.jpg",
						description: "",
						selected: true

					},
				]



			};
		},

		methods: {
			validateBeforeSubmitNewTalent() {
				this.$validator.validateAll().then((result) => {
					if (result) {
					// eslint-disable-next-line
					return;
					}
				});
			},
			optionTotal() {
				this.optionTotalVar = this.condidate.length;
				//alert(this.optionTotalVar)
			},
			//----- special list
			addForm() {
				this.isActive = true;
				this.optionTotal();

			},
			removeForm() {
				this.isActive = false;
				this.value = ""

			}
		},
		computed: {
			selectAll: {
				get: function () {
					return this.condidate ? this.checkedNames.length == this.condidate.length : false;
				},
				set: function (value) {
					var checkedNames = [];

					if (value) {
						this.condidate.forEach(function (user) {
							checkedNames.push(user.id);
						});
					}

					this.checkedNames = checkedNames;
				}
			}
		}

	};
</script>


