<template>
  <div class="">
    <div class="cand-card__full-name-edit clearfix">
      <span class="float-left">
        <span class="mr-5 cand-card__name" v-show="!fullNameShowBoo">{{
          firstName + " " + lastName
        }}</span>
        <span class="cand-card__name cand-card_inline" v-show="fullNameShowBoo">
          <b-input
            id="firstName"
            type="text"
            maxlength="30"
            name="firstName"
            class=""
            v-model="firstName"
          ></b-input>
          <b-input
            id="lastName"
            type="text"
            maxlength="30"
            name="lastName"
            class=""
            v-model="lastName"
          ></b-input>
        </span>
        <i class="ic_edit mr-2" @click="fullNameShow"></i>
        <i class="ic_delete" v-b-modal.modalRemove></i>
      </span>
    </div>
    <div class="cand-card__addfrom mb-1">
      <i class="ic_AddList"></i>
      <span>   </span>
    </div>
    <div class="cand-card__addfrom">
      <i class="ic_activity"></i>
      <span>1 </span>
    </div>
  </div>
</template>

<script>
export default {
  name: "CandidateUserDetails",
  data() {
    return {
      firstName: "",
      lastName: "",
      fullNameShowBoo: false,
    };
  },
  methods: {
    fullNameShow() {
      this.fullNameShowBoo = !this.fullNameShowBoo;
    },
  },
};
</script>


