<template>
  <div class="cand-card__status-bar">
    <div class="d-flex cand-card__status-bar-navigator pb-2">
      <div class="user-info">
        <h2>   </h2>
        <SelectDropDown
          :defaultOption="dropdown_1"
          :options="options"
          noBorder="true"
        />
      </div>

      <div class="mr-auto">
        <div class="d-flex align-items-center cand-card__status-reject-bar">
          <button class="btn ml-2 mr-2 btn-success btn-sm">
             
          </button>
          <div class="cand-card__status-bar-top d-flex mr-2">
            <div class="cand-card__status-bar-next cand-card__status-icon">
              <i class="ic_LeftArrow"></i>
            </div>
            <div class="cand-card__status-title"> </div>
            <div class="cand-card__status-bar-prev cand-card__status-icon">
              <i class="ic_RightArrow"></i>
            </div>
          </div>

          <b-nav class="mr-2 pl-2 pr-2" text="Left align">
            <SelectDropDown
              :defaultOption="dropdown_2"
              :options="options2"
              noBorder="true"
            />
          </b-nav>

          <b-nav
            class="cand-card__reject mr-2 select-dropmenu select-dropmenu_border-last-child"
            text="Left align"
          >
            <b-nav-item-dropdown right no-caret>
              <template slot="button-content">
                <div class="cand-card__status-icon">
                  <i class="inc_person_off_black font-24"></i>
                </div>
              </template>
              <b-dropdown-item> </b-dropdown-item>
              <b-dropdown-item>  / </b-dropdown-item>
              <b-dropdown-item>   </b-dropdown-item>
              <b-dropdown-item>     </b-dropdown-item>
              <b-dropdown-item><i class="ic_reject"></i> </b-dropdown-item>
            </b-nav-item-dropdown>
          </b-nav>

          <b-nav
            class="cand-card__reject mr-2 select-dropmenu select-dropmenu_border-last-child"
            text="Left align"
          >
            <b-nav-item-dropdown right no-caret>
              <template slot="button-content">
                <div class="cand-card__status-icon">
                  <i class="inc_delete_forever_black font-24"></i>
                </div>
              </template>
              <b-dropdown-item> </b-dropdown-item>
              <b-dropdown-item>  / </b-dropdown-item>
              <b-dropdown-item>   </b-dropdown-item>
              <b-dropdown-item>     </b-dropdown-item>
              <b-dropdown-item><i class="ic_reject"></i> </b-dropdown-item>
            </b-nav-item-dropdown>
          </b-nav>

          <b-nav class="cand-card__reject" text="Left align">
            <b-nav-item-dropdown right no-caret>
              <template slot="button-content">
                <div class="cand-card__status-icon">
                  <i class="inc_close_black font-24" />
                </div>
              </template>
            </b-nav-item-dropdown>
          </b-nav>
        </div>
      </div>
    </div>

    <div
      class="cand-card__status-bar-wrapper d-flex align-items-center justify-content-center"
    >
      <div
        class="row align-items-center justify-content-center flex-row-reverse"
      >
        <div
          class="col cand-card__status-bar-col flex justify-content-center d-flex cand-card__status-bar__tick"
          data-number="1"
        >
          <span class="cand-card__status-bar-title text-truncate"></span>
        </div>
        <div
          class="col cand-card__status-bar-col flex justify-content-center d-flex cand-card__status-bar__tick"
          data-number="2"
        >
          <span class="cand-card__status-bar-title text-truncate"></span>
        </div>
        <div
          class="col cand-card__status-bar-col flex justify-content-center d-flex cand-card__status-bar__tick"
          data-number="3"
        >
          <span class="cand-card__status-bar-title text-truncate"
            > </span
          >
        </div>
        <div
          class="col cand-card__status-bar-col flex justify-content-center d-flex"
          data-number="4"
        >
          <span class="cand-card__status-bar-title text-truncate"
            > </span
          >
        </div>
        <div
          class="col cand-card__status-bar-col flex justify-content-center d-flex"
          data-number="5"
        >
          <span class="cand-card__status-bar-title text-truncate"></span>
        </div>
      </div>
    </div>
  </div>
</template>
<style lang="scss">
.user-info {
  h2 {
    font-size: 16px;
    font-weight: bold;
  }
}

.cand-card__status-title {
  height: 30px;
  min-width: 142px;
  border-left: 2px solid #fff;
  border-right: 2px solid #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 5px;
}
.cand-card__status-icon {
  width: 30px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.cand-card__status-bar {
  padding: 15px 0 25px;
  overflow-x: visible !important;
  background: none;
  box-shadow: 0 3px 3px 0 rgba(0, 0, 0, 0.16);
  z-index: 11;
  position: relative;
  .nav-link {
    color: #393e7a;
    padding: 0px !important;
  }
}
.cand-card__status-bar-navigator {
  padding: 0 10px;
  margin-bottom: 15px;
  border-bottom: 2px dashed #e1e1e1;
}
.cand-card__status-bar-wrapper {
  margin-top: 25px;
  & > div {
    min-width: 650px;
  }
}
.cand-card__status-reject-bar {
  > * {
    background: #e1e1e1;
    height: 30px;
    min-width: 30px;
    justify-content: center;
    align-items: center;
    border-radius: 2px;
    font-weight: bold;
  }
  .cand-card__reject {
    &.select-dropmenu {
      min-width: auto;
      li .nav-link {
        padding-bottom: 0 !important;
        padding-top: 0 !important;
        i {
          margin-right: initial !important;
        }
      }
    }
  }
}
.cand-card__status-bar-title {
  bottom: -15px !important;
  top: initial !important;
  font-size: 13px !important;
  max-width: 150px;
  text-align: center;
}
.cand-card .cand-card__status-bar-col:last-child:before {
  right: 0;
}
.cand-card .cand-card__status-bar-col:before {
  right: 0;
  left: 0;
  margin: 0 auto;
  text-align: center;
  height: 28px;
  width: 28px;
  font-size: 15px;
  align-items: center;
  justify-content: center;
  display: flex;
  content: attr(data-number);
}
.cand-card .cand-card__status-bar-col.cand-card__status-bar__tick {
  &:before {
    content: "\E938";
  }
  .cand-card__status-bar-title {
    color: #393e7a;
  }
}
.cand-card .cand-card__status-bar-col:after {
  right: initial;
  left: -40%;
  width: 80%;
  bottom: 17px;
}
</style>
<script>
import SelectDropDown from "../SelectDropDown";

export default {
  name: "CandidateCardStatusBar",
  components: {
    SelectDropDown,
  },
  data() {
    return {
      dropdown_1: {
        title: " ",
        icon: "inc_double-thumb_up_black-2",
        active: true,
      },
      dropdown_3: { title: "", icon: "inc_person_off_black", active: false },
      dropdown_2: { title: " ", active: false },
      options: [
        {
          title: " ",
          icon: "inc_double-thumb_up_black-2",
          active: true,
        },
        { title: "", icon: "inc_thumb_up_black", active: true },
        { title: " ", icon: "inc_thumb_down_black", active: true },
        { title: " ", icon: "inc_thumbs_up_down_black", active: true },
      ],
      options2: [
        { title: " 1 ", active: true },
        { title: " 12 ", active: true },
        { title: " 123 ", active: true },
      ],
    };
  },
  methods: {
    setDropDown1(value) {
      this.dropdown_1 = value;
    },
    setDropDown2(value) {
      this.dropdown_2 = value;
    },
    candidateStatus() {
      var candCardStatus = document.querySelectorAll(
        ".cand-card__status-bar-col"
      );
      var candCardStatusNext = document.querySelector(
        ".cand-card__status-bar-next"
      );
      var candCardStatusPrev = document.querySelector(
        ".cand-card__status-bar-prev"
      );
      candCardStatusNext.onclick = function () {
        var candCardStatusTick = document.querySelectorAll(
          ".cand-card__status-bar__tick"
        );
        if (5 > candCardStatusTick.length) {
          candCardStatus[candCardStatusTick.length].click();
        }
      };
      candCardStatusPrev.onclick = function () {
        var candCardStatusTick = document.querySelectorAll(
          ".cand-card__status-bar__tick"
        );
        if (candCardStatusTick.length - 2 > -1) {
          candCardStatus[candCardStatusTick.length - 2].click();
        }
      };
      for (var i = 0; i < candCardStatus.length; i++) {
        candCardStatus[i].onclick = function () {
          var candCardStatusText = this.innerText;
          document.querySelector(
            ".cand-card__status-title"
          ).innerHTML = candCardStatusText;
          if (this.classList.contains("cand-card__status-bar__tick")) {
            var indexOfClick = Array.from(candCardStatus).indexOf(event.target);
            for (var j = indexOfClick + 1; j < candCardStatus.length; j++) {
              candCardStatus[j].classList.remove("cand-card__status-bar__tick");
            }
          } else {
            var indexOfClick = Array.from(candCardStatus).indexOf(event.target);
            for (var j = indexOfClick; j > 0; j--) {
              if (
                !candCardStatus[j].classList.contains(
                  "cand-card__status-bar__tick"
                )
              ) {
                candCardStatus[j].classList.add("cand-card__status-bar__tick");
              }
            }
          }
        };
      }
    },
  },
  mounted() {
    this.candidateStatus();
  },
};
</script>


