<template>
  <div class="cand-card__user-review cand-card__tab-page">
    <div class="cand-card__expert">
      <div class="d-flex align-items-center">
        <span class="mr-3">  </span>
        <SelectDropDown
          :defaultOption="defaultOption"
          :options="options"
          fill="true"
          noBorder="true"
        />
      </div>
      <div
        class="
          user-review__all-rate
          d-flex
          align-items-center
          justify-content-around
          mt-5
        "
      >
        <div
          class="d-flex justify-content-center flex-column align-items-center"
          v-for="(input, index) in reviewGroup"
          :key="index"
          @mouseover="hoverOver(input.team)"
          @mouseout="hoverOut(null)"
        >
          <i :class="input.icon" />
          <div class="pt-2 cand-card__expert-labelbox">
            <div class="cand-card__expert-labelbox-inner">
              <span class="cand-card__expert-percentage">%</span>
              <span class="cand-card__expert-label">
                {{ input.label }}
              </span>
            </div>
          </div>
        </div>
      </div>
      <div class="cand-card__expert-all-team" v-if="team">
        <div class="user-review-body row align-items-center mr-0 ml-0">
          <div
            v-for="(teamMember, index) in team"
            :key="index"
            class="
              user-review-body__item
              col-sm-3
              d-flex
              align-items-center
              justify-content-center
              flex-column
            "
          >
            <div class="title text-truncate w-100 text-center">
              {{ teamMember }}
            </div>
          </div>
        </div>
      </div>
    </div>

    <ul class="cand-card__rate-board">
      <li>
        <div class="cand-card__rate-board-header d-flex align-items-center">
          <span class="title d-flex align-items-center">
            <i class="inc_person_outline_black font-24 mr-1" />
            </span
          >
          <span class="cand-card__star-rate mr-auto"
            ><i class="ic_StarFull"></i> <i class="ic_StarFull"></i>
            <i class="ic_StarFull"></i> <i class="ic_star"></i>
            <i class="ic_star"></i>
          </span>
        </div>
      </li>

      <li>
        <div class="cand-card__rate-board-header d-flex align-items-center">
          <span class="title d-flex align-items-center">
            <i class="inc_person_outline_black font-24 mr-1" />
            </span
          >
          <span class="cand-card__star-rate mr-auto"
            ><i class="ic_StarFull"></i> <i class="ic_StarFull"></i>
            <i class="ic_StarFull"></i> <i class="ic_star"></i>
            <i class="ic_star"></i>
          </span>
        </div>
        <div
          class="
            user-review-body
            row
            align-items-center
            justify-content-start
            mr-0
            ml-0
          "
        >
          <div
            v-for="(input, index) in reviewGroup2"
            :key="index"
            class="
              user-review-body__item
              col-sm-3
              d-flex
              align-items-center
              justify-content-start
              flex-column
            "
          >
            <div class="cand-card__star-rate mb-2">
              <i class="ic_StarFull"></i> <i class="ic_StarFull"></i>
              <i class="ic_StarFull"></i> <i class="ic_star"></i>
              <i class="ic_star"></i>
            </div>
            <div class="title text-truncate w-100 text-center">
                
            </div>
          </div>
        </div>
      </li>
    </ul>
  </div>
</template>
<style lang="scss">
.user-review-body__item {
  border-bottom: 1px dashed #e1e1e1;
  border-left: 1px dashed #e1e1e1;
  margin: 0 !important;
  padding: 10px;
  margin: 0px !important;
}
.cand-card__expert-all-team {
  background: white;
  width: 100%;
  margin: 0 auto;
  border: 1px solid #e1e1e1;
  .user-review-body {
    padding: 15px;
  }
}
.cand-card .cand-card__rate-board li {
  border-color: #e1e1e1;
  padding: 0;
  > * {
    padding: 10px;
  }
  .user-review-body {
    border-top: 1px solid #e1e1e1;
    .cand-card__star-rate {
      > * {
        margin-right: 3px;
        font-size: 12px;
      }
    }
  }
}
.cand-card .cand-card__star-rate {
  direction: ltr;
  display: flex;
  align-items: center;
  font-size: 16px;
  > * {
    margin-right: 3px;
    color: #e1e1e1;
  }
}
.cand-card__expert {
  border-bottom: 1px solid #ccc;
  padding-bottom: 15px;
  margin-bottom: 15px;
  .cand-card__expert-labelbox {
    .cand-card__expert-labelbox-inner {
      position: relative;
    }
    &:hover {
      color: #ffb300;
    }
  }
  .user-review__all-rate {
    i {
      font-size: 42px;
      color: #393e7a;
    }
  }
  .cand-card__expert-label {
    font-size: 40px;
  }
  .cand-card__expert-percentage {
    position: absolute;
    top: 10px;
    font-size: 15px;
    right: -10px;
  }
}
.cand-card__rate-board {
  .user-review-header {
    .title {
      font-size: 14px;
    }
  }
}
</style>
<script>
import SelectDropDown from "../SelectDropDown";
export default {
  name: "CandidateUserDetails",
  components: {
    SelectDropDown,
  },
  data() {
    return {
      reviewGroup: [
        {
          icon: "inc_double-thumb_up_black-2",
          label: "40",
          team: ["  1 ", " ", " ", " "],
        },
        {
          icon: "inc_thumb_up_black",
          label: "30",
          team: ["  2 ", " ", " ", " "],
        },
        {
          icon: "inc_thumb_down_black",
          label: "20",
          team: ["  3 ", " ", " ", " "],
        },
        {
          icon: "inc_thumbs_up_down_black",
          label: "10",
          team: ["  4 ", " ", " ", " "],
        },
      ],
      reviewGroup2: [1, 2, 3, 4, 5, 6],
      team: null,
      defaultOption: { title: " ", active: true },
      options: [
        { title: " ", active: true },
        { title: " ", active: true },
      ],
    };
  },
  methods: {
    hoverOver(input) {
      this.team = input;
    },
    hoverOut(input) {
      this.team = input;
    },
  },
};
</script>


