<script>
// import candformMoreItem from './candform-more-item'
import candformApplicat from "./candform-applicat";
import FormApplicant from "./FormApplicant";
import FormApplicantBig from "./FormApplicantBig";
import candformCandcard from "./candform-candcard";
import modalRemove from "../components/modalRemove";

export default {
  name: "candform-show-det",
  components: {
    // candformMoreItem,
    candformApplicat,
    FormApplicant,
    FormApplicantBig,
    candformCandcard,
    modalRemove,
  },
  data() {
    return {
      showModal: false,
      activeSubrow: false,
      candidateUploadBar: false,
      modalShow: false,
      selectRemoveToggle: false,
    };
  },
  methods: {
    selectCheckBox() {
      var selectB = this.$el.querySelectorAll(
        ".ul-checkbox__input .custom-control-input"
      );
      var selectRT = this.$el.querySelector(".select-remove-toggle");
      var selectRTCount = 0;
      var i = 0;
      selectB[0].onclick = function () {
        if (this.checked) {
          selectRT.style.display = "block";
          selectRTCount = selectB.length - 1;
        } else {
          selectRTCount = 0;
          if (selectRTCount == 0) {
            selectRT.style.display = "none";
          }
        }
      };
      for (i = 1; i < selectB.length; i++) {
        selectB[i].onclick = function () {
          if (this.checked) {
            selectRT.style.display = "block";
            selectRTCount = selectRTCount + 1;
          } else {
            selectRTCount = selectRTCount - 1;
            if (selectRTCount == 0) {
              selectRT.style.display = "none";
            }
            if (selectRTCount < selectB.length) {
              selectB[0].checked = false;
            }
          }
          // console.log(selectRTCount)
        };
      }
    },

    selectAll() {
      var selectT = this.$el.querySelectorAll(
        ".ul-checkbox__input .custom-control-input"
      );
      if (selectT[0].checked) {
        for (var i = 0; i < selectT.length; i++) {
          selectT[i].checked = true;
        }
      } else {
        for (var i = 0; i < selectT.length; i++) {
          selectT[i].checked = false;
        }
      }
    },
    showRowdetail() {
      this.activeSubrow = !this.activeSubrow;
      // this.tableOddColor();
    },
    tableOddColor() {
      var tableOddColorEle = document.querySelectorAll(".table-subrow__row");
      for (var i = 0; i < tableOddColorEle.length; i++) {
        tableOddColorEle[2 * i].classList.add("table-subrow_bg");
      }
    },
    candidateUploadBarEv() {
      this.candidateUploadBar = !this.candidateUploadBar;
    },
    trigger() {
      this.$refs.onFileChange.click();
    },
  },
  mounted() {
    this.selectCheckBox();
    // this.tableOddColor();
  },
};
</script>


