
<template>
  <b-nav
    :class="[
      'select-dropmenu',
      className,
      { 'select-dropmenu_fill': fill, 'select-dropmenu_no-border': noBorder },
    ]"
    text="right align"
  >
    <b-nav-item-dropdown right no-caret :class="{ active: dropdown.active }">
      <template slot="button-content">
        <span class="d-flex align-items-center">
          <i :class="dropdown.icon" v-if="dropdown.icon" />
          <span
            v-if="dropdown.title"
            :class="[
              {
                'pr-2': dropdown.icon,
              },
            ]"
          >
            {{ dropdown.title }}
          </span>
        </span>
        <i class="ic_DownArrow" v-if="dropdown.title" />
      </template>
      <b-dropdown-item
        v-for="(input, index) in options"
        :key="index"
        @click="setDropDown1(input)"
      >
        <i :class="input.icon" v-if="input.icon" />
        {{ input.title }}
      </b-dropdown-item>
    </b-nav-item-dropdown>
  </b-nav>
</template>
<style lang="scss">
.select-dropmenu {
  text-align: right;
  min-width: 140px;
  &_fill {
    background-color: #e1e1e1;
    .active > .nav-link {
      color: #393e7a;
    }
  }
  .active > .nav-link {
    color: #393e7a;
  }
  &_no-border {
    li .nav-link {
      border-bottom: 0 !important;
    }
  }
  li {
    width: 100%;
    .nav-link {
      padding: 5px !important;
      display: flex;
      color: #7b7b7b;
      border-bottom: 1px solid #e1e1e1;
      i {
        margin-right: auto !important;
      }
      > span {
        i {
          font-size: 21px;
        }
      }
    }
    .dropdown-menu {
      border: 0;
      box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.47);
      background-color: #ffffff;
      .dropdown-item {
        border-bottom: 0;
        color: #7b7b7b;
        display: flex;
        align-items: center;
        i {
          color: #7b7b7b;
        }
        &:focus {
          background-color: transparent;
          color: #7b7b7b;
        }
        &:hover {
          background-color: #e1e1e1;
          color: #393e7a;
          i {
            color: #393e7a;
          }
        }
      }
    }
  }
  &.select-dropmenu_border-last-child li .dropdown-menu .dropdown-item {
    &:last-child {
      border-top: 1px dashed #707070;
    }
  }
}
</style>
<script>
export default {
  name: "SelectDropDown",

  data() {
    return {
      dropdown: this.defaultOption,
    };
  },
  props: ["className", "defaultOption", "options", "fill", "noBorder"],
  methods: {
    setDropDown1(value) {
      this.dropdown = value;
    },
  },
};
</script>


