<template>
  <header class="app-header navbar">
    <button class="navbar-toggler mobile-sidebar-toggler d-lg-none" type="button" @click="mobileSidebarToggle">
      <span class="navbar-toggler-icon"></span>
    </button>
    <b-link class="navbar-brand d-sm-down-none" to="#"></b-link>
    <button class="navbar-toggler sidebar-toggler d-md-down-none" type="button" @click="sidebarToggle">
      <span class="navbar-toggler-icon"></span>
    </button>


    <b-navbar-nav class="d-md-down-none">
      <HeaderShortcuts/>
      <!-- <b-nav-item class="px-3">Shortcuts</b-nav-item> -->
    </b-navbar-nav>
    
	
	
	
  <!-- profile side menu -->
	<b-nav class="ml-auto navbar-nav">

      <HeaderAddnew/>
      <b-nav-item class="d-sm-down-none">
        <i class="ic_web mr-2"></i>
         
      </b-nav-item>
      <b-nav-item class="d-sm-down-none">
        <i class="ic_MyCompany mr-2"></i>
         
      </b-nav-item>
      <HeaderProfile/>

    </b-nav>



    <!-- <button class="navbar-toggler aside-menu-toggler d-md-down-none" type="button" @click="asideToggle">
      <span class="navbar-toggler-icon"></span>
    </button> -->
  </header>
</template>
<script>
  import HeaderProfile from './HeaderProfile.vue'
  import HeaderShortcuts from './HeaderShortcuts.vue'
  import HeaderAddnew from './HeaderAddnew.vue'

  export default {
    name: 'header',
    components: {
      HeaderProfile,
      HeaderShortcuts,
      HeaderAddnew
    },
    methods: {
      sidebarToggle (e) {
        e.preventDefault()
        document.body.classList.toggle('sidebar-hidden')
      },
      sidebarMinimize (e) {
        e.preventDefault()
        document.body.classList.toggle('sidebar-minimized')
      },
      mobileSidebarToggle (e) {
        e.preventDefault()
        document.body.classList.toggle('sidebar-mobile-show')
      },
      asideToggle (e) {
        e.preventDefault()
        document.body.classList.toggle('aside-menu-hidden')
      }
    }
  }
</script>



