<template>
  <div class="cand-card__history cand-card__tab-page">
    <b-form @submit.prevent="validateBeforeSubmit" class="mb-3">
      <div class="field-group bg-white">
        <div class="form-row">
          <b-form-fieldset class="col-sm-6">
            <b-form-input
              name="feedbackName"
              v-model="feedbackName"
              placeholder="  "
              type="text"
              maxlength="100"
            ></b-form-input>
          </b-form-fieldset>

          <b-form-fieldset class="col-sm-6">
            <b-form-input
              name="position"
              v-model="position"
              type="text"
              placeholder="/    "
            ></b-form-input>
          </b-form-fieldset>

          <b-form-fieldset class="col-sm-12">
            <b-form-input
              name="feedbackContact"
              v-model="feedbackContact"
              v-validate="'numeric'"
              :class="{
                input: true,
                'border-danger': errors.has('feedbackContact'),
              }"
              type="text"
              placeholder="   "
              maxlength="30"
            ></b-form-input>
            <span
              v-show="errors.has('feedbackContact')"
              class="help-valid text-danger"
              >this field required</span
            >
          </b-form-fieldset>

          <b-form-fieldset class="col-sm-12">
            <b-form-textarea
              type="text"
              v-model="feedbackDescription"
              :rows="3"
              :max-row="6"
              placeholder=" "
              maxlength="1000"
            ></b-form-textarea>
          </b-form-fieldset>
          <b-form-fieldset class="col-sm-6">
            <div class="mt-1">
              <SelectDropDown
                :defaultOption="defaultOption"
                :options="options"
              />

              <!-- <b-form-select
                :plain="true"
                :options="[
                  ' ',
                  '  ',
                  '  ',
                  '  ',
                ]"
                value=" "
              ></b-form-select> -->
            </div>
          </b-form-fieldset>
          <b-form-fieldset class="col-sm-6">
            <div class="clearfix mt-1">
              <button
                type="submit"
                class="btn float-right ml-2 btn-primary btn-sm"
              >
                
              </button>
              <button
                type="button"
                class="btn float-right btn btn-link btn-secondary btn-sm"
              >
                
              </button>
            </div>
          </b-form-fieldset>
        </div>
      </div>
    </b-form>

    <ul class="cand-card__rate-board">
      <li v-for="(input, index) in jobsGroup" :key="index">
        <div
          class="
            cand-card__rate-board-header
            d-flex
            align-items-center
            pt-2
            pb-2
          "
        >
          <span class="option__image rounded-circle rounded-circle_smallsize">
            <img
              src="static/img/avatars/66.jpg"
              class="option__image rounded-circle"
              style="width: 35px; height: 35px"
            />
          </span>
          <p class="mb-0 pr-2 pl-4">{{ input.title }}</p>
          <span class="mr-auto review-control d-flex">
            <span class="d-flex align-items-center pl-4">
              <i class="inc_thumb_up_black mr-2" />
              <span>  </span>
            </span>
            <span class="mt-1" style="width: 65px">
              <span class="d-flex">
                <i
                  class="inc_delete_forever_black mr-2"
                  v-show="!isActive"
                  @click="editTalent"
                />

                <span class="mr-2" v-show="isActive">
                  <i class="inc_close_black" @click="editTalent" />
                  <i class="ic_tick mr-2" @click="editTalent" />
                </span>

                <span v-show="!isActive">
                  <i class="inc_edit_black" @click="editTalent" />
                  <i class="inc_info_black" @click="editTalent" />
                </span>
              </span>
            </span>
          </span>
        </div>

        <div class="user-review-body">
          <p>{{ input.description }}</p>
        </div>
      </li>
    </ul>
  </div>
</template>
<style lang="scss">
.cand-card__jobs {
  input[type="checkbox"]:checked + span + span {
    text-decoration: line-through;
  }
  .custom-control.custom-checkbox {
    display: flex;
    align-items: center;
  }
  .review-control {
    font-size: 12px;
  }
}
</style>
<script>
import SelectDropDown from "../SelectDropDown";

export default {
  name: "Jobs",
  components: {
    SelectDropDown,
  },
  data() {
    return {
      feedbackContact: "",
      feedbackName: "",
      feedbackDescription: "",
      position: "",
      isActive: false,
      jobsGroup: [
        {
          title: " ",
          description: `
          :  (   )                                   .                            
          `,
        },
        {
          title: " ",
          description: `
          :  (   )                                   .                            
          `,
        },
        {
          title: " ",
          description: `
          :  (   )                                   .                            
          `,
        },
        {
          title: " ",
          description: `
          :  (   )                                   .                            
          `,
        },
      ],
      isActive: false,
      defaultOption: { title: " ", active: false },
      options: [
        { title: " ", active: false },
        { title: "  ", active: true },
        { title: "  ", active: true },
        { title: "  ", active: true },
      ],
    };
  },

  methods: {
    editTalent() {
      this.isActive = !this.isActive;
    },
  },
};
</script>


